import { Component, Input, OnInit } from '@angular/core';
import { Sucursal } from 'src/app/interfaces/sucursales';
import { SettingsService } from 'src/app/services/settings.service';
import { SucursalesService } from 'src/app/services/sucursales.service';
import { UserService } from 'src/app/services/user.service';

declare function init_sideBar();

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styles: [
  ]
})
export class SidebarComponent implements OnInit { 

@Input() user;
@Input() apiUrl: string;
public menu = [];
public indice;
public sucursales: Sucursal[];

  constructor(private userService: UserService,
              private settingsService: SettingsService,
              private sucursalesService: SucursalesService) { }

  ngOnInit(): void {

  
    this.settingsService.mostrarMenu.subscribe( user => {
        this.user = user;
        this.iniciarSideBar();
    });

    this.settingsService.actualizarInfo.subscribe( () => {
        this.user = JSON.parse( localStorage.getItem('user') );
    });

    if (this.user && this.user.validada === 1 && ( (this.user.tipoEmpresa && (this.user.tipoEmpresa === 'IPS' || this.user.tipoEmpresa === 'Consultorio')) || this.user.tipoEmpresa === 'empleado' )  ) {
      setTimeout(() => {
        document.getElementById('sw-sucus').click();
       }, 500);
 

    }

    this.iniciarSideBar();
  }

  iniciarSideBar() {

    this.menu = JSON.parse(this.userService.getLocalStorage('menu'));

    switch (this.user.tipoCuenta) {

      case 'empresa' :

        this.sucursalesService.getSucursales(this.user.idEmpresa).subscribe( res => this.sucursales = res );

        if (this.user.tipoEmpresa === 'Consultorio' ) {
          this.indice = '/consultorio';
          init_sideBar();
        }

        if (this.user.tipoEmpresa === 'IPS' ) {
        this.indice = '/ips';
        init_sideBar();
        }

        if (this.user.tipoEmpresa === 'Veterinaria' ) {
          this.indice = '/veterinaria';
          init_sideBar();
          }

        break;

      case 'empleado' :

        this.indice = '/empleado';
        this.sucursales = this.user.sucursales;
        init_sideBar();
        break;
      case 'medico' :
        this.indice = '/medico';
        init_sideBar();
        break;
    }

  }


  cambiarASucursal(sucursal: Sucursal) {
    this.sucursalesService.cambiarASucursal(sucursal, this.user.tipoCuenta);
  }

}
