import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { SucursalesService } from 'src/app/services/sucursales.service';
import { UserService } from 'src/app/services/user.service';
import { AplicationService } from '../../services/aplication.service';
import { Sucursal, FotoSucursal } from '../../interfaces/sucursales';
import { environment } from 'src/environments/environment.prod';
import { PermisosService } from 'src/app/services/permisos.service';

@Component({
  selector: 'app-gestionar-sucursales',
  templateUrl: './gestionar-sucursales.component.html',
  styles: [
  ]
})
export class GestionarSucursalesComponent implements OnInit {

  public load = false;
  register = false;
  public formSucursal: FormGroup;
  public arrayImagenes: Array<any> = [];
  public imagenSubir: File;
  public fileName: string;
  public filePreview: string;
  public depasMunis;
  public buscarDepasMunis;
  public user;
  public sucursales: Sucursal[];
  public apiUrl = environment.apiUrl;
  public estado: string;
  public sucursalEdit: Sucursal;
  public imagenesEliminar = [];
  public estadoSucursal;
  public sucursal;


  constructor(private appService: AplicationService,
    private sanitizer: DomSanitizer,
    private userService: UserService,
    private sucursalService: SucursalesService,
    private permisos: PermisosService) { }

  ngOnInit(): void {
    this.user = JSON.parse(this.userService.getLocalStorage('user'));
    this.getSucursales();
  }

  abrirModalSucursal(estado: string, sucursal?: Sucursal) {

    if (this.permisos.permisos(1, 1, 2, estado)) {

      this.estado = estado;
      this.sucursalEdit = sucursal;
      this.inicializarFormulario(this.estado);
      this.imagenesEliminar = [];
      this.arrayImagenes = [];
      document.getElementById('btn-sucursal').click();
    }

  }

  inicializarFormulario(estado: string) {

    if (estado === 'crear') {

      this.formSucursal = new FormGroup({
        nombreSucursal: new FormControl('', [Validators.required]),
        direccionSucursal: new FormControl('', [Validators.required]),
        barrioSucursal: new FormControl('', [Validators.required]),
        telefonoSucursal: new FormControl(''),
        celularSucursal: new FormControl(''),
        idmunicipio: new FormControl('', [Validators.required]),
        nombreMunicipio: new FormControl('')
      });


    } else {

      this.formSucursal = new FormGroup({
        nombreSucursal: new FormControl(this.sucursalEdit.nombreSucursal, [Validators.required]),
        direccionSucursal: new FormControl(this.sucursalEdit.direccionSucursal, [Validators.required]),
        barrioSucursal: new FormControl(this.sucursalEdit.barrioSucursal, [Validators.required]),
        telefonoSucursal: new FormControl(this.sucursalEdit.telefonoSucursal),
        celularSucursal: new FormControl(this.sucursalEdit.celularSucursal, [Validators.required]),
        idmunicipio: new FormControl(this.sucursalEdit.idmunicipio, [Validators.required]),
        nombreMunicipio: new FormControl(this.sucursalEdit.municipio)
      });


    }

    this.getDepasMunis();
  }
  setTimeLoad() {
    this.load = true;
    setTimeout(() => {
      this.load = false,
        this.register = true;
    }, 500);
  }
  /**
   * Metodo que cambia la imagen por defecto y muestra la cargada para previsualizacion, 
   * la variable imagenSubir cambia a file para la subida de imagen.
   * @param event
   */
  // tslint:disable-next-line:typedef
  previsualizarImagen(event) {


    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      // console.log(event.target.files);
      this.imagenSubir = file;
      reader.readAsDataURL(file);

      if (file.type === 'image/png' ||
        file.type === 'image/jpg' ||
        file.type === 'image/jpeg' ||
        file.type === 'image/PNG' ||
        file.type === 'image/JPG' ||
        file.type === 'image/JPEG') {
        reader.onload = () => {
          this.fileName = file.name + ' ' + file.type;
          this.filePreview = 'data:image/png' + ';base64,' + (<string>reader.result).split(',')[1];
          if (this.arrayImagenes.length <= 5) {

            let favorita: number;

            if (this.estado === 'crear') {

              if (this.arrayImagenes.length <= 0) {
                favorita = 1;
              } else {
                favorita = 0;
              }

            } else {


              if (this.arrayImagenes.length <= 1 && this.sucursalEdit.fotos.length <= 0) {
                favorita = 1;
              } else {
                favorita = 0;
              }
            }


            this.arrayImagenes.push({ foto: this.filePreview, favorita });
          }
          else {
            this.appService.swal('Advertencia', 'Solo se admiten subir 6 imagenes.', 'warning');
          }
        };
      } else {
        this.appService.swal('Advertencia', 'Solo se admiten imagenes tipo png, jpg, jpeg.', 'warning');
      }
    }
  }

  /**
   * Metodo para obtener la url de la app para previsualizar imagenes.
   * @return url completa 
   * @param url
   */
  // tslint:disable-next-line:typedef
  sanitize(url: string) {
    return this.sanitizer.bypassSecurityTrustUrl(url);
  }

  /**
 * Metodo que permite eliminar la previsualizacion de imagenes de un servicio segun su indice.
 * @param i
 */
  // tslint:disable-next-line:typedef
  borrarFoto(i) {
    this.arrayImagenes.splice(i, 1);
  }


  crearEditarSucursal() {

    // Crear Sucursal
    if (this.estado === 'crear') {

      let info = {
        nombreSucursal: this.formSucursal.get('nombreSucursal').value,
        direccionSucursal: this.formSucursal.get('direccionSucursal').value,
        barrioSucursal: this.formSucursal.get('barrioSucursal').value,
        idEmpresa: this.user.idEmpresa,
        idmunicipio: this.formSucursal.get('idmunicipio').value,
        telefonoSucursal: this.formSucursal.get('telefonoSucursal').value,
        celularSucursal: this.formSucursal.get('celularSucursal').value,
        fotos: this.arrayImagenes,
        estado: 2
      };

      // console.log(info);
      this.sucursalService.postCrearSucursal(info).subscribe(() => { this.getSucursales() });

    } else {

      // Editar Sucursal
      let info = {
        nombreSucursal: this.formSucursal.get('nombreSucursal').value,
        direccionSucursal: this.formSucursal.get('direccionSucursal').value,
        barrioSucursal: this.formSucursal.get('barrioSucursal').value,
        idEmpresa: this.user.idEmpresa,
        idmunicipio: this.formSucursal.get('idmunicipio').value,
        telefonoSucursal: this.formSucursal.get('telefonoSucursal').value,
        celularSucursal: this.formSucursal.get('celularSucursal').value,
        fotosN: this.arrayImagenes,
        fotosE: this.imagenesEliminar,
        idSucursales: this.sucursalEdit.idSucursales
      };

      console.log(info);
      this.sucursalService.putEditarSucursal(info).subscribe(() => { this.getSucursales(); document.getElementById('btn-cerrar-sucursalModel').click() });
    }


  }

  getDepasMunis() {
    this.appService.getDeparMunis().subscribe(res => this.depasMunis = res);
  }

  deparMuniBuscar(value: string) {
    this.buscarDepasMunis = value;
  }


  // borrarFotoServicio(imagen, posicion: number) {

  //   if ( (this.servicio.fotos.length + this.arrayImagenes.length) <= 1 ) {
  //    this.appService.swal('Advertencia', 'El servicio debe contener al menos una imagen', 'warning');
  //    return;
  //   }

  //   this.servicio.fotos.splice(posicion, 1);
  //   this.imagenesEliminar.push({idFotoServicio: imagen.idfotosServicios, path : imagen.path });

  // }

  selecionarMunicipio(municipio) {
    this.buscarDepasMunis = null;
    (<HTMLInputElement>document.getElementById('idmunicipio')).value = municipio.nombreMunicipio + ' - ' + municipio.nombreDepartamento;
    this.formSucursal.get('idmunicipio').setValue(municipio.idmunicipio);
  }

  getSucursales() {
    this.sucursalService.getSucursales(this.user.idEmpresa).subscribe(res => this.sucursales = res);
  }

  eliminarSucursal(sucursal: Sucursal) {

    if (this.permisos.permisos(1, 1, 2, 'eliminar')) {

      if (sucursal.principal == 1) return this.appService.swal('', 'No es posible eliminar la sucursal principal.', 'warning');

      this.appService.swalConfirm('', ` Estas seguro que deseas eliminar ${sucursal.nombreSucursal} ?`, 'error').then(() => {
        // console.log('eliminar sucursal');}
        this.sucursalService.putEliminarSucursal(sucursal.idSucursales).subscribe(() => { this.getSucursales(); });
      });
    }
  }


  borrarFotoSucursal(imagen: FotoSucursal, posicion: number) {

    if ((this.sucursalEdit.fotos.length + this.arrayImagenes.length) <= 1) {
      this.appService.swal('Advertencia', 'El servicio debe contener al menos una imagen', 'warning');
      return;
    }

    this.sucursalEdit.fotos.splice(posicion, 1);
    this.imagenesEliminar.push({ idFotosSucursal: imagen.idFotosSucursal, path: imagen.path });
  }


  cambiarASucursal(sucursal: Sucursal) {
    this.sucursalService.cambiarASucursal(sucursal, this.user.tipoCuenta);
  }

  modalSucursalInactiva(sucursal: Sucursal) {

    this.sucursal = sucursal;
    this.sucursalService.getValidacionSucursal(sucursal.idSucursales).subscribe((res) => {
      this.estadoSucursal = res;
      document.getElementById('btn-modal-estadoSucu').click();
    });

  }

  redireccion(ruta: string) {

    let usuario;
    if (this.user.tipoCuenta === 'empresa') {
      usuario = this.user.tipoEmpresa;
    } else {
      usuario = this.user.tipoCuenta;
    }

    document.getElementById('btn-cerrar-modal-estadoSucu').click();
    this.cambiarASucursal(this.sucursal);
    this.appService.redireccion(usuario, ruta);
  }

}


