import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { PagesComponent } from './pages.component';
import { RolesPermisosComponent } from '../components/crear-empresa/roles-permisos/roles-permisos.component';

// GUARDS
import { AuthGuard } from '../services/guards/auth.guard';
import { RedireccionGuard } from '../services/guards/redireccion.guard';
import { ConsultorioModule } from '../modulos/consultorio_pages/consultorio.module';
import { GestionarPersonalComponent } from '../modulos/consultorio_pages/gestionar-personal/gestionar-personal.component';
import { GestionarServiciosComponent } from '../modulos/consultorio_pages/gestionar-servicios/gestionar-servicios.component';
import { GestionarConsultoriosComponent } from '../modulos/consultorio_pages/gestionar-consultorios/gestionar-consultorios.component';
import { CalendarioComponent } from '../components/calendario/calendario.component';
import { CitasComponent } from '../components/citas/citas.component';
import { PerfilComponent } from './perfil/perfil.component';
import { PermisosGuard } from '../services/guards/permisos.guard';
import { HistoriaClinicaOptometriaComponent } from '../components/historia-clinica-optometria/historia-clinica-optometria.component';
import { GestionarPacientesComponent } from '../components/gestionar-pacientes/gestionar-pacientes.component';
import { GestionarSucursalesComponent } from '../components/gestionar-sucursales/gestionar-sucursales.component';
import { GestionarPromocionesComponent } from '../components/gestionar-promociones/gestionar-promociones.component';


const routes: Routes = [

  {
  path: '',
  component: PagesComponent,
  canActivate: [RedireccionGuard],
  children: [
    { path: 'home', component: HomeComponent, data: {titulo : 'Home'} },
    { path: '', redirectTo: '/home', pathMatch: 'full' }
  ]},
  {
  path: 'consultorio',
  component: PagesComponent,
  canActivate: [AuthGuard],
  children: [
    { path: 'home', component: CitasComponent, data: {titulo : 'Administrar Citas'} },
    { path: 'personal', component: GestionarPersonalComponent, data : { titulo: 'Gestionar Personal'}},
    { path: 'servicios', component: GestionarServiciosComponent, data : { titulo: 'Gestionar Servicios'}},
    { path: 'consultorios', component: GestionarConsultoriosComponent, data : { titulo: 'Gestionar Consultorios'}},
    { path: 'agenda', component: CalendarioComponent, data : { titulo: 'Agenda'}},
    { path: 'citas', component: CitasComponent, data : { titulo: 'Administrar Citas'}},
    { path: 'perfil', component: PerfilComponent, data : { titulo: 'Mi perfil'}},
    { path: 'sucursales', component: GestionarSucursalesComponent, data : { titulo: 'Gestionar Sucursales'}},
    { path: 'promociones', component: GestionarPromocionesComponent, data : { titulo: 'Gestionar Promociones'}},
    { path: '', redirectTo: 'citas', pathMatch: 'full' },
  ]},
  {
    path: 'ips',
    component : PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'home', component: CitasComponent, data: {titulo : 'Administrar Citas'} },
      { path: 'personal', component: GestionarPersonalComponent, data : { titulo: 'Gestionar Personal'}},
      { path: 'servicios', component: GestionarServiciosComponent, data : { titulo: 'Gestionar Servicios'}},
      { path: 'consultorios', component: GestionarConsultoriosComponent, data : { titulo: 'Gestionar Consultorios'}},
      { path: 'agenda', component: CalendarioComponent, data : { titulo: 'Agenda'}},
      { path: 'citas', component: CitasComponent, data : { titulo: 'Administrar Citas'}},
      { path: 'perfil', component: PerfilComponent, data : { titulo: 'Mi perfil'}},
      { path: 'sucursales', component: GestionarSucursalesComponent, data : { titulo: 'Gestionar Sucursales'}},
      { path: 'promociones', component: GestionarPromocionesComponent, data : { titulo: 'Gestionar Promociones'}},
      { path: '', redirectTo: 'citas', pathMatch: 'full' },
    ]
  },
  {
    path: 'veterinaria',
    component : PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'home', component: CitasComponent, data: {titulo : 'Administrar Citas'} },
      { path: 'personal', component: GestionarPersonalComponent, data : { titulo: 'Gestionar Personal'}},
      { path: 'servicios', component: GestionarServiciosComponent, data : { titulo: 'Gestionar Servicios'}},
      { path: 'consultorios', component: GestionarConsultoriosComponent, data : { titulo: 'Gestionar Consultorios'}},
      { path: 'agenda', component: CalendarioComponent, data : { titulo: 'Agenda'}},
      { path: 'citas', component: CitasComponent, data : { titulo: 'Administrar Citas'}},
      { path: 'perfil', component: PerfilComponent, data : { titulo: 'Mi perfil'}},
      { path: 'sucursales', component: GestionarSucursalesComponent, data : { titulo: 'Gestionar Sucursales'}},
      { path: 'promociones', component: GestionarPromocionesComponent, data : { titulo: 'Gestionar Promociones'}},
      { path: '', redirectTo: 'citas', pathMatch: 'full' },
    ]
  },
  {
    path: 'empleado',
    component : PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'home', component: CitasComponent, data: {titulo : 'Administrar Citas'} },
      { path: 'personal', component: GestionarPersonalComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Personal', m: 1, op: 0, sm: 0}},
      { path: 'servicios', component: GestionarServiciosComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Servicios', m: 1, op: 1, sm: 0}},
      { path: 'consultorios', component: GestionarConsultoriosComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Consultorios', m: 1, op: 1, sm: 1}},
      { path: 'agenda', component: CalendarioComponent, canActivate: [PermisosGuard], data : { titulo: 'Agenda', m: 0, op: 0, sm: 1}},
      { path: 'citas', component: CitasComponent, canActivate: [PermisosGuard], data : { titulo: 'Administrar Citas', m: 0, op: 0, sm: 0}},
      { path: 'perfil', component: PerfilComponent, data : { titulo: 'Mi perfil'}},
      { path: 'sucursales', component: GestionarSucursalesComponent, data : { titulo: 'Gestionar Sucursales', m: 1, op: 1, sm: 2}},
      { path: 'promociones', component: GestionarPromocionesComponent, data : { titulo: 'Gestionar Promociones', m: 1, op: 1, sm: 3}},
      { path: '', redirectTo: '/home', pathMatch: 'full' },
    ]
  },
  { path: 'medico',
    component : PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'home', component: CitasComponent, data: {titulo : 'Administrar Citas'} },
      { path: 'personal', component: GestionarPersonalComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Personal', m: 1, op: 0, sm: 0}},
      { path: 'servicios', component: GestionarServiciosComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Servicios', m: 1, op: 1, sm: 0}},
      { path: 'consultorios', component: GestionarConsultoriosComponent, canActivate: [PermisosGuard], data : { titulo: 'Gestionar Consultorios', m: 1, op: 1, sm: 1}},
      { path: 'agenda', component: CalendarioComponent, canActivate: [PermisosGuard], data : { titulo: 'Agenda', m: 0, op: 0, sm: 1}},
      { path: 'citas', component: CitasComponent, canActivate: [PermisosGuard], data : { titulo: 'Administrar Citas', m: 0, op: 0, sm: 0}},
      { path: 'perfil', component: PerfilComponent, data : { titulo: 'Mi perfil'}},
      { path: 'historia-clinica', component: HistoriaClinicaOptometriaComponent, data : { titulo: 'Historia clinica optometria'}},
      { path: 'pacientes', component: GestionarPacientesComponent, data : { titulo: 'Gestionar Pacientes'}},
      { path: '', redirectTo: '/home', pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
