import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MedicosService } from 'src/app/services/medicos.service';
import { UserService } from 'src/app/services/user.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-gestionar-pacientes',
  templateUrl: './gestionar-pacientes.component.html',
  styles: [
  ]
})
export class GestionarPacientesComponent implements OnInit {
  public medico: any;
  public citasHistorial: any;
  public apiUrl = environment.apiUrl;

  constructor(private medicoService: MedicosService,
              private userService: UserService) { }

  ngOnInit(): void {
    this.medico = JSON.parse(this.userService.getLocalStorage('user'));
  }

  buscarPaciente(formBuscar: NgForm) {
    this.medicoService.getPaciente({ numeroIdentificacion : formBuscar.value.numeroDocumento, idMedico : this.medico.idMedico }).subscribe( res => {
      console.log(res);
      this.citasHistorial = res;
    });
  }

}
