import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { InfoServicio } from 'src/app/interfaces/servicios';
import { AplicationService } from 'src/app/services/aplication.service';
import { SettingsService } from 'src/app/services/settings.service';
import { SucursalesService } from 'src/app/services/sucursales.service';
import { environment } from 'src/environments/environment.prod';
import * as moment from 'moment';
import { PermisosService } from 'src/app/services/permisos.service';
import { Promocion } from 'src/app/interfaces/sucursales';

@Component({
  selector: 'app-gestionar-promociones',
  templateUrl: './gestionar-promociones.component.html',
  styles: [
  ]
})

export class GestionarPromocionesComponent implements OnInit {
  public load = false;
  public apiUrl = environment.apiUrl;
  public arrayImagenes: Array<any> = [];
  public imagenSubir: File;
  public fileName: string;
  public filePreview: string;
  public serviciosActivos: InfoServicio[];
  public user;
  public formPromocion: FormGroup;
  public hoy;
  public promociones: Promocion[];
  public estado: string;
  public promocion: Promocion;

  constructor(private sanitizer: DomSanitizer,
              private appService: AplicationService,
              private sucursalService: SucursalesService,
              private settingsService: SettingsService,
              private permisos: PermisosService) { }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('user'));
    this.hoy = this.appService.hoy('hoy', '', 0);
    this.getServiciosActivosSucursal();
    this.getPromociones();

    this.settingsService.actualizarInfo.subscribe( () => {
      this.user = JSON.parse(localStorage.getItem('user'));
      this.getServiciosActivosSucursal();
      this.getPromociones();
    });

    this.inicializarFormulario();
  }

  inicializarFormulario() {

    this.formPromocion = new FormGroup({
        titulo: new FormControl( '', Validators.required ),
        fechaInicio: new FormControl( '', Validators.required),
        fechaFinalizacion: new FormControl( '', Validators.required),
        servicio: new FormControl( '', Validators.required),
        descripcion: new FormControl( '', [Validators.required, Validators.maxLength(500)])
    });
  }

  crearPromocion() {

    if(this.permisos.permisos(1, 1, 3, 'crear')) {

      if(this.serviciosActivos.length <= 0) {
        this.appService.swalConfirm('', 'Antes de crear una promocin tienes que tener al menos un servicio activo, para activarlo debes asociar un servicio a un consultorio,  Deseas hacerlo ahora?', 'warning')
                        .then( ( ) => {
                          let usuario;
                          if (this.user.tipoCuenta === 'empresa') {
                            usuario = this.user.tipoEmpresa;
                          } else {
                            usuario = this.user.tipoCuenta;
                          }
                      
                          this.appService.redireccion(usuario, 'consultorios');
                        });

        return;
    }

    this.estado = 'crear';
    document.getElementById('btn-modal-promociones').click();

    }
 
  }

 
  /**
  * Metodo que permite obtener los servicios activos
  */
    getServiciosActivosSucursal() {
        this.sucursalService.getServiciosActivosSucursal({ idMunicipio: this.user.idmunicipioSucursal, idSucursal: this.user.idSucursales })
          .subscribe(res => this.serviciosActivos = res );
    }


    crearPublicacion() {

      if (this.arrayImagenes.length <= 0) 
      return this.appService.swal('', 'Es necesario cargar al menos una imagen para crear la promocin.', 'warning');
      
      // moment(new Date()).format('HH:mm:ss');
      // let fechaInicio = moment(this.formPromocion.get('fechaInicio').value);
      // let fechaFinalizacion = moment(this.formPromocion.get('fechaFinalizacion').value);
      // if ( fechaFinalizacion.diff(fechaInicio, 'd') < 1 ) 
      // return this.appService.swal('', 'La promocin debe tener una duracin de al menos un da de duracin.', 'warning');
      
      let info = {
          titulo: this.formPromocion.get('titulo').value,
          fechaInicio: `${this.formPromocion.get('fechaInicio').value}T${moment(new Date()).format('HH:mm:ss')}`,
          fechaFinalizacion:`${this.formPromocion.get('fechaFinalizacion').value}T${moment(new Date()).format('HH:mm:ss')}`,
          descripcion: this.formPromocion.get('descripcion').value,
          idServicios: this.formPromocion.get('servicio').value,
          idSucursales: this.user.idSucursales,
          imagenes: this.arrayImagenes
      };
      // console.log(info);
      this.sucursalService.postCrearPromocion(info).subscribe( () => { 
          document.getElementById('btn-cerrar-modalpromociones').click();
          // Get Promociones
          this.getPromociones();
      });
    }


    getPromociones () {

      this.sucursalService.getPromociones(this.user.idSucursales).subscribe( (res) => {
        this.promociones = res;
      });
    }

    alertConfirmEliminarPromo(promocion: Promocion) {

      if(this.permisos.permisos(1, 1, 3, 'eliminar')) { 
        
        this.appService.swalConfirm('', `Estas seguro que deseas eliminar ${promocion.titulo}?, Ten en cuenta que dejara de ser visible para los usuarios.`, 'warning').then( () => {
          
          this.sucursalService.dltPromocion(promocion.idPromociones).subscribe( () => { 
            this.getPromociones();
          });
        });
      }
    }

    editarPromocion(promocion: Promocion) {
      
      if(this.permisos.permisos(1, 1, 3, 'editar')) { 
        this.promocion = promocion;
        this.estado = 'editar';
        document.getElementById('btn-modal-promociones').click();

      }
    }

  // <!-- ----------------------------------------------------------------------------------------------------- -->
  // <!-- ---------------------------------------- METODOS IMAGENES ------------------------------------------- -->
  // <!-- ----------------------------------------------------------------------------------------------------- -->


    /**
   * Metodo que cambia la imagen por defecto y muestra la cargada para previsualizacion, 
   * la variable imagenSubir cambia a file para la subida de imagen.
   * @param event
   */ 
  // tslint:disable-next-line:typedef
  previsualizarImagen(event) {


    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0 ) {
      let file = event.target.files[0];
      // console.log(event.target.files);
      this.imagenSubir = file;
      reader.readAsDataURL(file);

      if (file.type === 'image/png' ||
      file.type === 'image/jpg' ||
      file.type === 'image/jpeg' ||
      file.type === 'image/PNG' ||
      file.type === 'image/JPG' ||
      file.type === 'image/JPEG') {
        reader.onload = () => {
          this.fileName = file.name + ' ' + file.type;
          this.filePreview = 'data:image/png' + ';base64,' + ( <string> reader.result ).split(',')[1];
          if ( (this.arrayImagenes.length + 1 ) <= 3){

            this.arrayImagenes.push(this.filePreview);
          }
          else{
            this.appService.swal('Advertencia', 'Solo se admiten subir 3 imagenes.', 'warning');
          }
        };
    }else{
      this.appService.swal('Advertencia', 'Solo se admiten imagenes tipo png, jpg, jpeg.', 'warning');
    }
  }
}


  /**
   * Metodo para obtener la url de la app para previsualizar imagenes.
   * @return url completa 
   * @param url
   */
  // tslint:disable-next-line:typedef
  sanitize(url: string) {
    return this.sanitizer.bypassSecurityTrustUrl(url);
  }

      /**
   * Metodo que permite eliminar la previsualizacion de imagenes de un servicio segun su indice.
   * @param i
   */
  // tslint:disable-next-line:typedef
  borrarFoto(i) {
    this.arrayImagenes.splice(i, 1);
   }


  // <!-- ----------------------------------------------------------------------------------------------------- -->
  // <!-- -------------------------------------- FIN METODOS IMAGENES ----------------------------------------- -->
  // <!-- ----------------------------------------------------------------------------------------------------- -->

}
