import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CalendarView } from 'angular-calendar';

@Component({
  selector: 'mwl-demo-utils-calendar-header',
  template: `
    <div class="row text-center">
      <div class="col-md-4">
        <div class="btn-group">
          <div
            class="btn waves-effect waves-light btn-rounded btn-outline-info "
            mwlCalendarPreviousView
            [view]="view"
            [(viewDate)]="viewDate"
            (viewDateChange)="viewDateChange.next(viewDate)"
          >
            Anterior
          </div>
          <div
            id="hoy"
            class="btn waves-effect waves-light btn-rounded btn-outline-warning"
            mwlCalendarToday
            [(viewDate)]="viewDate"
            (viewDateChange)="viewDateChange.next(viewDate)"
          >
             Hoy
          </div>
          <div
            class="btn waves-effect waves-light btn-rounded btn-outline-info"
            mwlCalendarNextView
            [view]="view"
            [(viewDate)]="viewDate"
            (viewDateChange)="viewDateChange.next(viewDate)"
          >
            Siguiente

          </div>
        </div>
      </div>
      <div class="col-md-4">
        <h3>{{ viewDate | calendarDate: view + 'ViewTitle':locale }}</h3>
      </div>
      <div class="col-md-4">
        <div class="btn-group">
          <div
            class="btn waves-effect waves-light btn-rounded btn-outline-info"
            (click)="viewChange.emit(CalendarView.Month)"
            [class.active]="view === CalendarView.Month"
          >
            Mes
          </div>
          <div
            class="btn waves-effect waves-light btn-rounded btn-outline-info"
            (click)="viewChange.emit(CalendarView.Week)"
            [class.active]="view === CalendarView.Week"
          >
            Semana
          </div>
          <div
            class="btn waves-effect waves-light btn-rounded btn-outline-info"
            (click)="viewChange.emit(CalendarView.Day)"
            [class.active]="view === CalendarView.Day"
          >
            Dia
          </div>
          <button type="button" class="btn btn-block waves-effect waves-light btn-rounded btn-outline-info" data-toggle="tooltip" data-placement="bottom" title="Refrescar calendario" onclick="location.reload()">
            <i class="fas fa-sync"></i>
            </button>
        </div>
      </div>
    </div>
    <br />
  `,
})

export class CalendarHeaderComponent { 
  @Input() view: CalendarView;

  @Input() viewDate: Date;

  @Input() locale: string = 'es';

  @Output() viewChange = new EventEmitter<CalendarView>();

  @Output() viewDateChange = new EventEmitter<Date>();

  CalendarView = CalendarView;

  constructor() {
    setTimeout(() => {
      document.getElementById('hoy').click();
    }, 10);
  }

}
