import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../services/user.service';
declare var particlesJS: any;

@Component({
  selector: 'app-info-noticias',
  templateUrl: './info-noticias.component.html',
  styleUrls: ['./carpet/css/mobster.css','./carpet/css/maicons.css','./carpet/css/animate.css']

})
export class InfoNoticiasComponent implements OnInit {

  constructor(private userService: UserService,
    public router: Router) { }

  ngOnInit(): void {
    let info = JSON.parse(this.userService.getLocalStorage('user'));
    if (info) {
      this.router.navigate(['']);
      return;
      
  }



  
  }

}
