  // ======================================
  // Get sucursales, segun el idEmpresa
  // ======================================
  getSucursales(idEmpresa: number) {
    return this.http.get( apiUrl + '/sucursales/' + idEmpresa, { headers})
                    .map( (res: any) => {

                      return res.body;
                    })
                    .catch( err => {
                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw Error(err);
                    });
  }

