  // ======================================
  // Mtodo para cambiar de sesin a una sucursal, cuando eres IPS o 
  // empleado con permisos sobre sucursal
  // ======================================
  cambiarASucursal(sucursal: Sucursal, tipo: string) {
    this.appService.swalConfirm('', `Se cambiara de sesin a ${sucursal.nombreSucursal}, desde la cul podras administrar empleados y consultorios para la sucursal.` ,'success')
    .then( () => { 

      let user = JSON.parse(localStorage.getItem('user'));
      user.sucursal = sucursal;
      user.idSucursales = sucursal.idSucursales;
      user.principal = sucursal.principal;
      user.idmunicipioSucursal = sucursal.idmunicipio;
      user.nombreSucursal = sucursal.nombreSucursal;

      this.userService.saveLocalStorage(user, 'user');
      this.settingsService.actualizarInfo.emit(true);

      if(tipo === 'empleado') {
        this.userService.getMenuParametros({ idMembers: user.idMembers, idSucursal: user.idSucursales, plataforma : 'WEB' }).subscribe( (menu) => {
          this.userService.saveLocalStorage(menu[0].menu, 'menu');
          this.settingsService.mostrarMenu.emit( user );
        });

      }

      this.appService.swal('', `Cambio de sesin a ${sucursal.nombreSucursal}`, 'success');

    });
  }

