import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { environment } from '../../environments/environment.prod';
import { AplicationService } from './aplication.service';
import { Router } from '@angular/router';
import { Login } from '../interfaces/users';
import { throwError } from 'rxjs';
import { SettingsService } from './settings.service';

const headers = new HttpHeaders().set('Content-Type', 'application/json');
const apiUrl = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient,
              private appService: AplicationService,
              private router: Router,
              private settingsService: SettingsService
              ) { }


  // ======================================
  // Guardar info en el localStorage
  // ======================================
  saveLocalStorage(info, clave) {
    localStorage.removeItem(clave);
    localStorage.setItem(clave, JSON.stringify(info));
  }

  // ======================================
  // Get info del localStorage
  // ======================================
  getLocalStorage(clave) {
    return localStorage.getItem(clave);
  }

  // ======================================
  // Get menu, parametro el tipo de usuario
  // ======================================
  getMenu(tipoUser: string) {
    return this.http.get( apiUrl + '/info/menu/' + tipoUser, { headers})
                    .map( (res: any) => {
                      // console.log(res.menu.menu);
                      return res.menu.menu;

                    })
                    .catch( err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw throwError(err);
                    });
  }

  // ======================================
  // ConfirmarCuenta, recibe como parametro info,
  // con informacion del memberId, el codigo de confirmacion,
  // la opcion de confirmacion, y el telefono en caso de ser
  // la opcion elegida
  // ======================================

  confirmarCuenta(info) {
    return this.http.post( apiUrl + '/confirmarcuenta', info, {headers} )
                    .map( (res:any) => {
                      if (!res.ok) {
                        this.appService.swal('Advertencia', res.mensaje, 'warning');
                        return;
                      }

                      this.appService.swal('Bienvenido', res.mensaje, 'success');
                      // cerrar modal de confirmacion de cuenta
                      document.getElementById('btn-cerrar-modal-confirmacion').click();

                      // Actualizar informacion de la cuenta
                      let info = JSON.parse(this.getLocalStorage('user'));
                      this.getInfoUser({idMembers: info.idMembers, tipoUser: info.tipoCuenta }).subscribe();

                      // Respuesta al metodo
                      return true;
                    } )
                    .catch( err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw throwError(err);
                    });
  }


  // ======================================
  // Reenviar cdigo de confirmacin
  // ======================================
  reenviarCodigoConfirmacion(idMembers) {
      return this.http.get(apiUrl + '/reenviarcodigo/' + idMembers, {headers})
                      .map( (res: any) => {

                        if (!res.ok) {
                          this.appService.swal('Advertencia', res.mensaje, 'warning');
                          return;
                        }

                        this.appService.swal('Exito', res.mensaje, 'success');
                        return true;
                      })
                      .catch( err => {

                        if (err.status === 0) {
                          this.appService.swal
                          ('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                          return err;
                        }

                        this.appService.swal('Error', err.error.mensaje, 'error');
                        throw throwError(err);
                      });
  }

  // ======================================
  // Metodo para ontener informacion del usuario,
  // recibe como parametro el idMembers y el tipo de cuenta
  // ======================================
  getInfoUser(info) {
    return this.http.get(apiUrl + '/infouser/' + info.idMembers + '/' + info.tipoUser, { headers})
                    .map( (res: any) => {
                      
                      if (!res.ok) {
                        // Mensaje para que vuelva a iniciar sesion
                        this.appService.swal('Advertencia', 'Hubo un conflicto al actulizar informacin, por favor vuelve a iniciar sesin', 'warning');
                        this.logOut();
                        return;
                      }

                
                      if (res.body.tipoCuenta === 'empleado') {

                        // Si el empleado solo pertenece a una sucursal.
                        // if (res.body.sucursales.length <= 1) {
                        //   res.body.idSucursales = res.body.sucursales[0].idSucursales;
                        //   res.body.idEmpresa = res.body.sucursales[0].idEmpresa;
                        // } 
                        let empleado = JSON.parse( localStorage.getItem('user') );
                        this.getMenuParametros({idMembers: empleado.idMembers, idSucursal: empleado.idSucursales, plataforma : 'WEB'}).subscribe( menu => {
                          this.saveLocalStorage(menu[0].menu, 'menu'); 
                          
                        });

                      }

         
                      if(res.body.tipoCuenta !== 'empleado') {
                        let menu;
                          // Buscar menu plataforma web y lo guarda en variable let menu.
                          res.menu.map( m => {
                            if (m.plataforma === 'WEB') {
                                menu = m.menu;
                            }
                          });


                          this.saveLocalStorage(menu, 'menu');
                        
                      }



                      this.saveLocalStorage(res.body, 'user');
                      this.saveLocalStorage(res.token, 'token');
                      
                      return res.body;

                    })
                    .catch(err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      // this.appService.swal('Error', err.error.mensaje, 'error');
                      throw throwError(err);
                      });
  }


  // ======================================
  // Metodo para enviar codigo de confirmacion de cuenta
  // a telfono celular
  // ======================================
  enviarCodigoTelefono(info) {
    return this.http.post(apiUrl + '/enviarsmsconfirmacion', info, {headers})
                    .map( (res: any) => {

                      // El mensaje reboto el numero de telfono puede que no sea correcto
                      if (!res.ok) {
                        this.appService.swal('Informacin', res.mensaje, 'warning');
                        return;
                      }

                      this.appService.swal('Exito', res.mensaje, 'success');
                      return true;
                    })
                    .catch(err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw throwError(err);
                    });
  }

  // ======================================
  // Metodo para login, recibe como informacion
  // usuario y contrasena
  // ======================================
  login(login: Login) {
    return this.http.post(apiUrl + '/login', login, {headers} )
                    .map( (res:any) => {

                      // console.log(res);
                      if (!res.ok) {
                        this.appService.swal('Advertencia', res.mensaje, 'warning');
                        return;
                      }

                      if (res.body.tipoCuenta === 'usuario' || res.body.tipoCuenta === 'beneficiario') {
                        this.appService.swal('Advertencia', 'Esta es una cuenta de usuario por favor inicia sesin desde la aplicacin movil.', 'warning');
                        return;
                      }

                      // Si inicia sesion el empleado.
                      if (res.body.tipoCuenta === 'empleado') {

                        // Si el empleado solo pertenece a una sucursal.
                        this.loginEmpleado(res).then( () => {
                          return true;
                        }).catch ( () => {
                          return false;
                        });


                        return;
                      }

                      // if (res.body.tipoCuenta === 'medico') {
                      //   this.appService.swal('', 'Estamos trabajando en el inicio de sesin para mdicos, por favor intentalo ms tarde.', 'success');
                      //   return;
                      // }

                      // Buscar menu plataforma web y lo guarda en variable let menu.
                      let menu;
                      res.menu.map( m => {
                        if (m.plataforma === 'WEB') {
                            menu = m.menu;
                        }
                      });

                      this.saveLocalStorage(res.body, 'user');
                      this.saveLocalStorage(res.token, 'token');
                      this.saveLocalStorage(menu, 'menu');
                      this.router.navigate(['']);
                      this.appService.toast('Prevenir Descuentos Mdicos', 'Bienvenido', {timeOut: 2000}, 'success');
                      return true;
                    })
                    .catch( err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }
                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw throwError(err);
                      } );
  }

  // ======================================
  // Funcion de login de empleado, para validar los casos para un empleado
  // 1. El empleado no pertenesca a ninguna sucursal
  // 2. Si el empleado pertenece a una sola sucursal, 
  // hacer peticion para obtener el menu y guardarlo en el local storage
  // 3.Si el empleado pertenece a mas de una sucursal
  // ======================================
  loginEmpleado(res) {
    
    return new Promise((resolve, reject) => {
          
      if (res.body.sucursales.length <= 0) {
        // Caso 1. Al empleado se le informa que no puede iniciar sesion desde la plataforma web
        this.appService.swal('', 'Tu cuenta no esta asociada como empleado a ninguna empresa, puedes seguir disfrutando de descuentos mdicos como usuario desde la aplicacin mvil.', 'warning');
        return reject(false);
      }

      if (res.body.sucursales.length === 1) {
        // Caso 1. Al empleado se le informa que no puede iniciar sesion desde la plataforma web
        this.getMenuParametros({ idMembers: res.body.idMembers, idSucursal: res.body.sucursales[0].idSucursales, plataforma : 'WEB'}).subscribe( menu => {
          res.body.idSucursales = res.body.sucursales[0].idSucursales;
          res.body.idEmpresa = res.body.sucursales[0].idEmpresa;
          res.body.principal = res.body.sucursales[0].principal;
          res.body.idmunicipioSucursal = res.body.sucursales[0].idmunicipio;
          res.body.nombreSucursal = res.body.sucursales[0].nombreSucursal;


          this.saveLocalStorage(res.body, 'user');
          this.saveLocalStorage(res.token, 'token');
          this.saveLocalStorage(menu[0].menu, 'menu');
          this.router.navigate(['']);
          this.appService.toast('Prevenir Descuentos Mdicos', 'Bienvenido', {timeOut: 2000}, 'success');

          return resolve(true);
        });
      }

      if (res.body.sucursales.length >= 2) { 
        this.saveLocalStorage(res.body, 'user');
        this.saveLocalStorage(res.token, 'token');
        this.router.navigate(['']);
        this.appService.toast('Prevenir Descuentos Mdicos', 'Bienvenido', {timeOut: 2000}, 'success');

        return resolve(true);
      }
    }); 
  }

  // ======================================
  // Mtodo para pedir el menu de un usuario segun parametros
  // ======================================
  getMenuParametros(parametros) {

    // console.log(parametros)
    return this.http.post( apiUrl + '/getmenu', parametros, { headers})
    .map( (res: any) => {
      // console.log(res);
      return res.menu;
    })
    .catch(err => {

      this.logOut();
      // console.log(err);
      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        return err;
      }

      this.appService.swal('Error', err.error.mensaje, 'error');
      throw Error(err);
    });
  }  

   // ======================================
  // Mtodo para actualizar el avatar de un usuario,
  // recibe como parametro el avatar, el idCuenta y el tipo de Cuenta
  // ======================================
  subirArchivo(avatar: File, idMembers: number, tipoImagen: string, favorito: number) {

    return new Promise((resolve, reject) => {

      let formData = new FormData();
      let xhr = new XMLHttpRequest();

      formData.append('imagen', avatar, avatar.name);

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {

          if (xhr.status === 200) {
            //  console.log('imagen subida');
            resolve(JSON.parse(xhr.response));
          } else {
            // console.log('fallo la subida');
            reject(JSON.parse(xhr.response));
          }
        }
      };

      let url = apiUrl + '/avataruser/' + idMembers + '/' + tipoImagen + '/' + favorito;
      xhr.open('PUT', url, true);
      xhr.send(formData);

    });
  }

  putActualizarEmpleado(empleado) {
    return this.http.put(apiUrl + '/perfil/empleado', empleado, { headers })
      .map((res: any) => {
        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
        // console.log(res);
        this.appService.swal('Exito', res.mensaje, 'success');
        return true;
      })
      .catch(err => {

        // console.log(err);
        if (err.status === 0) {
          this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
          return err;
        }

        this.appService.swal('Error', err.error.mensaje, 'error');
        throw Error(err);
      });
  }
  // ======================================
  // Log Out, metodo para cerra sesion y borrar
  // los datos del storage
  // ======================================
  logOut() {
    localStorage.clear();
    this.appService.toast('Prevenir Descuentos Mdicos', 'Vuelve pronto', {timeOut: 2000}, 'success');
    this.router.navigate(['/login']);
  }


}



