import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtrar'
})
export class FiltrarPipe implements PipeTransform {

  transform(arreglo: any[] , texto: string): unknown {
    if (texto === 'todos') {
      return arreglo;
  }

    texto = texto.toLocaleLowerCase();

    if (arreglo[0] && arreglo[0].tipoCuenta) {
      return arreglo = arreglo.filter((elemento) => {
        return (elemento.tipoCuenta.toLocaleLowerCase().indexOf(texto) > -1);
      });
    }

    if (arreglo[0] && arreglo[0].nombreEspecialidad) {
      return arreglo = arreglo.filter((elemento) => {
        return (elemento.nombreEspecialidad.toLocaleLowerCase().indexOf(texto) > -1);
      });
    }
    

  }

}
