import { Component, Input, OnInit } from '@angular/core';
import { SettingsService } from 'src/app/services/settings.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-modal-sucursal-empleado',
  templateUrl: './modal-sucursal-empleado.component.html',
  styles: [
  ]
})
export class ModalSucursalEmpleadoComponent implements OnInit {
  @Input() empleado;
  @Input() apiUrl: string;

  constructor(private userService: UserService,
              private settingsService: SettingsService) { }

  ngOnInit(): void {}

  sucursalSelect(sucursal) {

  this.userService.getMenuParametros({ idMembers: this.empleado.idMembers, idSucursal: sucursal.idSucursales, plataforma : 'WEB'}).subscribe( menu => {

    this.empleado.idSucursales = sucursal.idSucursales;
    this.empleado.idEmpresa = sucursal.idEmpresa;
    this.empleado.principal = sucursal.principal;
    this.empleado.idmunicipioSucursal = sucursal.idmunicipio;
    this.empleado.nombreSucursal = sucursal.nombreSucursal;

    this.userService.saveLocalStorage(this.empleado, 'user'); 
    this.userService.saveLocalStorage(menu[0].menu, 'menu'); 
    this.settingsService.mostrarMenu.emit( JSON.parse(localStorage.getItem('user')) );
    document.getElementById('btn-cerrar-empleadosucu').click();
    location.reload();

  });

  }
}
