import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AplicationService } from '../../services/aplication.service';
import { SettingsService } from '../../services/settings.service';
import { UserService } from '../../services/user.service';
import { Login } from '../../interfaces/users';
import { CambiarPsswComponent } from '../cambiar-pssw/cambiar-pssw.component';
declare function init_plugins();
declare var particlesJS: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
})
export class LoginComponent implements OnInit {
  @ViewChild(CambiarPsswComponent) cambiarpssw: CambiarPsswComponent;
  show_button: Boolean = false;
  show_eye: Boolean = false;
  constructor(public router: Router,
              private appService: AplicationService,
              private userService: UserService,
              private settingsService: SettingsService ) { }

  ngOnInit(): void {
    this.settingsService.setTema('light');
    init_plugins();
    this.configuracionLogin();

    particlesJS.load('particles-js', 'assets/particles.json', function() {
      // console.log('callback - particles.js config loaded');

    });
  }

  configuracionLogin() {
        // Evitar bug queda modal abierto
        let bug: any;
        bug = document.getElementsByClassName('modal-backdrop fade show');
        if (bug[0]) {
          bug[0].className = '';
        }

        let info = JSON.parse(this.userService.getLocalStorage('user'));
        if (info) {
          this.router.navigate(['']);
          return;
        }
  }


  login(formLogin: NgForm) {

    if (formLogin.invalid) {
      return;
    }

    let login: Login = {
      usuario : formLogin.value.usuario,
      contrasena : this.appService.encriptar(formLogin.value.contrasena)
    };
    // console.log(login); 
    this.userService.login(login).subscribe( );
  }
//Function
showPassword() {
  this.show_button = !this.show_button;
  this.show_eye = !this.show_eye;
}

  
}
