import { Component, Input, OnInit } from '@angular/core';
import { InfoEmpresa } from 'src/app/interfaces/users';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-modal-confirmar-cuenta',
  templateUrl: './modal-confirmar-cuenta.component.html',
  styles: [
  ]
})
export class ModalConfirmarCuentaComponent implements OnInit {
@Input() user: InfoEmpresa;
public codigo = '';
public celular = '';
public celularInput = false;
public loading = false;

  constructor(public userService: UserService) { }

  ngOnInit(): void {
    // Lanzar modal
    if (this.user.activa === 0) { 
      document.getElementById('btn-activar-cuenta').click();
    }
  }


  // Metodo para confirmar cuenta
  confirmar() {

    var opcion: string;
    // Confirmacion por celular
    if (this.celular.toString().length >= 10) {
      opcion = 'celular';
    } else {
      opcion = 'correo';
    }

    let info = { idMembers: this.user.idMembers, codigo: this.codigo, opcion, telefono: this.user.telefono };
    this.userService.confirmarCuenta(info).subscribe();
  }

  reenviarCodigo() {
    this.loading = true;
    this.userService.reenviarCodigoConfirmacion(this.user.idMembers).subscribe( () => this.loading = false );
  }

  enviarCodigoTelefono() {
    this.userService.enviarCodigoTelefono({numero: this.celular, idMembers: this.user.idMembers}).subscribe();
  }


}
