import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AplicationService } from 'src/app/services/aplication.service';

@Component({
  selector: 'app-completar-registro-users',
  templateUrl: './completar-registro-users.component.html',
  styles: [
  ]
})
export class CompletarRegistroUsersComponent implements OnInit {

  @Input() user;
  @Input() apiUrl: string;
  @Output() validada = new EventEmitter<boolean>();

  constructor(private appService: AplicationService) { }

  ngOnInit(): void {
    this.appService.setBreadcrumb('Completar perfil');
  }

  userValidado() {
    this.validada.emit(true);
  }

}
