  // ======================================
  // Mtodo para registrar un consultorio, recibe como
  // parameto un objeto tipo RegistroEmpresa.
  // ======================================
  postRegistroConsultorio(empresa: RegistroEmpresa) {
    return this.http.post(apiUrl + '/registro', empresa, { headers })
      .map((res: any) => {

        // console.log(res);
        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
        this.userService.saveLocalStorage(res.body, 'user');
        this.userService.saveLocalStorage(res.token, 'token');
        this.userService.saveLocalStorage(res.menu, 'menu');
        return true;
      })
      .catch(err => {
        // console.log(err);
        this.appService.swal('Error', err.error.mensaje, 'error');
        throw Error(err);
      });
  }


  // ======================================
