  putPerfilEmpresa(info) {
    // console.log(info);
    return this.http.put(apiUrl + '/perfil/empresa', info, {headers})
                    .map( (res: any) => {
                        // console.log('resss', res);
                        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
                        this.userService.saveLocalStorage(res.body, 'user');
                        this.userService.saveLocalStorage(res.token, 'token');
                        this.appService.swal('Correcto', res.mensaje, 'success');
                        return true;
                    })
                    .catch( err => {
                      // console.log(err);
                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw Error(err);
                    });
  }

  // ======================================
  // Mtodo para actualizar el avatar de un usuario,
  // recibe como parametro el avatar, el idCuenta y el tipo de Cuenta
  // ======================================
  subirArchivo(avatar: File, idMembers: number, tipoImagen: string, favorito: number) {

    return new Promise((resolve, reject) => {

      let formData = new FormData();
      let xhr = new XMLHttpRequest();

      formData.append('imagen', avatar, avatar.name);

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {

          if (xhr.status === 200) {
            //  console.log('imagen subida');
            resolve(JSON.parse(xhr.response));
          } else {
            // console.log('fallo la subida');
            reject(JSON.parse(xhr.response));
          }
        }
      };

      let url = apiUrl + '/avataruser/' + idMembers + '/' + tipoImagen + '/' + favorito;
      xhr.open('PUT', url, true);
      xhr.send(formData);

    });
  }


