       /**
     * Metodo que permite subir la imagen de perfil a la base de datos.
     */
    // tslint:disable-next-line:typedef
    subirArchivo(tipo) {
  
      if (!this.imagenSubir) {
        return;
      }
  
      this.empresaService.subirArchivo(this.imagenSubir, this.user.idMembers, tipo, 1).then(res => {
        this.appService.swal('Subir foto de ' + tipo, 'Imagen seleccionada como ' + tipo, 'success')
        this.userService.getInfoUser({idMembers: this.user.idMembers, tipoUser : this.user.tipoCuenta}).subscribe(
          res => {this.settingsService.actualizarInfo.emit(true);location.reload();})
      })
      .catch(err => console.log(err));
    }
  
       /**
     * Metodo que permite subir la imagen de perfil a la base de datos.
     */
    // tslint:disable-next-line:typedef
    selectPhoto(datos) {
      let idFotos =  typeof datos != 'object' ? datos : datos.getAttribute('data-idphoto');
      let type =  typeof datos != 'object' ? datos : datos.getAttribute('data-type');
  
      let info =  {
        idFotosperfil: idFotos,
        idMembers:this.user.idMembers,
        tipo:type,
      }
      // console.log(info);
      this.empresaService.postCambiarFotoPerfil(info).subscribe(()=>{
          this.userService.getInfoUser({idMembers: this.user.idMembers, tipoUser : this.user.tipoCuenta}).subscribe(
      res => {this.settingsService.actualizarInfo.emit(true);location.reload();})
        }
      );
    }
         /**
     * Metodo que permite subir la imagen de perfil a la base de datos.
     */
    // tslint:disable-next-line:typedef
    deletePhoto(datos) {
      let idFotos =  typeof datos != 'object' ? datos : datos.getAttribute('data-idphoto');
      let type =  typeof datos != 'object' ? datos : datos.getAttribute('data-type');
      let fav =  typeof datos != 'object' ? datos : datos.getAttribute('data-fav');
      let path =  typeof datos != 'object' ? datos : datos.getAttribute('data-path');
      // console.log(datos);
      let info =  {
        idFotosperfil: idFotos,
        idMembers:this.user.idMembers,
        tipo:type,
        favorita:fav,
        path:path,
      }
      // console.log(info);

      if(
      path == 'avatarundefined.png'||
      path == 'medico.png'||
      path == 'portada_medico.png'||
      path == 'background_default.png'||
      path == 'medical-2008481_640.png'){
        this.appService.swal('Advertencia', 'No puedes eliminar las imagenes por defecto, por favor aade fotos nuevas.', 'warning');
      }
      else
      this.empresaService.postEliminarFotoPerfil(info).subscribe(()=>{      
      this.userService.getInfoUser({idMembers: this.user.idMembers, tipoUser : this.user.tipoCuenta}).subscribe(
      res => {this.settingsService.actualizarInfo.emit(true);location.reload();})
        }
      );
    }
  
  


