import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { AplicationService } from 'src/app/services/aplication.service';

@Component({
  selector: 'app-cambiar-pssw',
  templateUrl: './cambiar-pssw.component.html',
  styles: [
  ]
})
export class CambiarPsswComponent implements OnInit {

  public email = new FormControl(null, [Validators.required, Validators.email]);

  constructor(private appService: AplicationService) { }

  ngOnInit(): void {
  }

  abrirModal() {
    document.getElementById('btn-cambiar-pssw').click();
  }

  verificarCorreo() {
    this.appService.verificarCorreo(this.email.value).subscribe();
  }

}
