import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { environment } from '../../../environments/environment.prod';

@Component({
  selector: 'app-crear-empresa',
  templateUrl: './crear-empresa.component.html'
})
export class CrearEmpresaComponent implements OnInit {

  @Input() user;
  @Output() validada = new EventEmitter<boolean>();

  public estado = 'perfil';
  public apiUrl = environment.apiUrl;

  constructor(private userService: UserService) { }

  ngOnInit(): void {
    // this.settingsService.setTema('light');
    // this.empresa = JSON.parse(this.userService.getLocalStorage('user'));
    this.user = JSON.parse(this.userService. getLocalStorage('user'));

    let estadoStorage = JSON.parse(this.userService.getLocalStorage('estado'));

    if (estadoStorage) {
        this.estado = estadoStorage;
    }
  }

  cambioEstado(estado) {

    if (estado === 'validada') {
      this.validada.emit(true);
      return;
    }

    this.estado = estado;
  }

}


