import { environment } from './../../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { InfoEmpresa } from 'src/app/interfaces/users';
import { SettingsService } from 'src/app/services/settings.service';
import { InfoServicio } from 'src/app/interfaces/servicios';
import { InfoPersonal } from 'src/app/interfaces/users';
declare function init_plugins();
declare function init_appInit();

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.component.html',
  styles: [
  ]
})
export class PerfilComponent implements OnInit {

  public apiUrl = environment.apiUrl;
  public empresa: InfoEmpresa;
  public servicios: InfoServicio;


  constructor(private settingsService: SettingsService,
    private userService: UserService) { }

  ngOnInit(): void {
    init_appInit();
    init_plugins();
    this.empresa = JSON.parse(this.userService. getLocalStorage('user'));

  }

}
