import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fotosFavoritas'
})
export class FotosFavoritasPipe implements PipeTransform {

  transform(value, tipo: string ) {

    if (Array.isArray(value)) {
      value = value.find(img => img.favorita == 1 && img.tipo == tipo);
    }

    return value;
  }
}
