import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fotosCarrusel'
})
export class FotosCarruselPipe implements PipeTransform {

  transform(value):Array<any> {
    
    if (value instanceof Array) {
      value = value.filter(img => (img.tipo == 'avatar'));
    }
    return value;
    
  }
}
