import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { AplicationService } from './aplication.service';

const headers = new HttpHeaders().set('Content-Type', 'application/json');
const apiUrl = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class MedicosService {

  constructor(private http: HttpClient,
              private appService: AplicationService) { }


  // ======================================
  // Mtodo para obtener las especialidades medicas
  // ======================================





  
  getEspecialidadesMedicas() {
    return this.http.get(apiUrl + '/especialidades' , {headers})
                    .map( (res: any) => {
                      return res.body;
                    })
                    .catch( err => {

                      if (err.status === 0) {
                        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
                        return err;
                      }

                      this.appService.swal('Error', err.error.mensaje, 'error');
                      throw Error(err);
                    });
  }

  putActualizarMedico(medico) {
    return this.http.put(apiUrl + '/perfil/medico', medico, { headers })
      .map((res: any) => {
        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
        // console.log(res);
        this.appService.swal('Exito', res.mensaje, 'success');
        return true;
      })
      .catch(err => {
        // console.log(err);

        if (err.status === 0) {
          this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
          return err;
        }

        this.appService.swal('Error', err.error.mensaje, 'error');
        throw Error(err);
      });
  }


  // ======================================
  // Get Mdicos, segun el id de Empresa
  // ======================================
  getMedicos(idSucursal: number) {

    return this.http.get(apiUrl + '/medicos/' + idSucursal , {headers})
        .map( (res: any) => {
          return res.body;
        })
        .catch( err => {

          if (err.status === 0) {
            this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
            return err;
          }

          this.appService.swal('Error', err.error.mensaje, 'error');
          throw Error(err);
        });
  }
    // ======================================
  // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
    actualizarEstudios(info) {

    return this.http.put(apiUrl + '/estudio/', info, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', 'Los cambios se realizaron con exito.', 'success');
      console.log(res);
      return res.body;
    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        console.log(err);
        return err; 
      }
      this.appService.swal('Error', err.error.mensaje, 'error');
      console.log(err);
      throw Error(err);
    });
  }
      // ======================================
  // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
  aadirEstudios(info) {

    return this.http.post(apiUrl + '/agregarestudio', info, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', 'Los cambios se realizaron con exito.', 'success');
      console.log(res);
      return res.body;
    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        console.log(err);
        return err; 
      }
      this.appService.swal('Error', err.error.mensaje, 'error');
      console.log(err);
      throw Error(err);
    });
  }

        // ======================================
  // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
  aadirTrayectoria(info) {

    return this.http.post(apiUrl + '/agregarexperiencia', info, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', 'Los cambios se realizaron con exito.', 'success');
      console.log(res);
      return res.body;
    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        console.log(err);
        return err; 
      }
      this.appService.swal('Error', err.error.mensaje, 'error');
      console.log(err);
      throw Error(err);
    });
  }

    // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
  deleteEstudios(id) {

    return this.http.delete(apiUrl + '/estudio/'+ id, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', 'Los cambios se realizaron con exito.', 'success');

      return res.body;

    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        return err;
      }


      this.appService.swal('Error', err.error.mensaje, 'error');
      throw Error(err);
    });
  }


    // ======================================
  // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
  actualizarExperiencia(info) {

    return this.http.put(apiUrl + '/experiencia/', info, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', res.mensaje , 'success');
      
      return res.body;

    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        console.log(err);
        return err; 
      }
      this.appService.swal('Error', err.error.mensaje, 'error');
      console.log(err);
      throw Error(err);
    });
  }

    // Mtodo para obtener los estudios del medico
  // que estan registrados en la base de datos
  // ======================================
  deleteExperiencia(id) {

    return this.http.delete(apiUrl + '/experiencia/'+ id, { headers })
    .map((res: any) => {
      this.appService.swal('Exito', 'Los cambios se realizaron con exito.', 'success');
      return res.body;

    }).catch(err => {

      if (err.status === 0) {
        this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
        return err;
      }


      this.appService.swal('Error', err.error.mensaje, 'error');
      throw Error(err);
    });
  }



  // ======================================
  // Get Mdicos, segun el id de Empresa
  // ======================================
  getPaciente(info) {
    return this.http.get(apiUrl +  `/citaspaciente/${info.numeroIdentificacion}/${info.idMedico}`,  { headers })
      .map((res: any) => {
        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
        // console.log(res);
        return res.body;
      })
      .catch(err => {

        if (err.status === 0) {
          this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
          return err;
        }

        this.appService.swal('Error', err.error.mensaje, 'error');
        throw Error(err);
      });
  }

  // ======================================
  // Get citas en curso de medico, segun el id del mdico
  // ======================================
    getCitasCursoMedico(idMedico: number) {

      return this.http.get(apiUrl +  `/citascursomedico/${idMedico}`,  { headers })
      .map((res: any) => {
        // Swal.fire('Registro', 'El registro fue exitoso', 'success');
        // console.log(res);
        return res.body;
      })
      .catch(err => {

        if (err.status === 0) {
          this.appService.swal('Error', 'Error en la conexin, por favor revisa tu conexin o intentalo ms tarde.', 'error');
          return err;
        }

        this.appService.swal('Error', err.error.mensaje, 'error');
        throw Error(err);
      });
    }
  

}
