import { NgModule } from '@angular/core';
import { Routes, RouterModule, NoPreloading } from '@angular/router';
import { LoginComponent } from './auth/login/login.component';
import { RegistroComponent } from './auth/registro/registro.component';
import { TerminosYCondicionesComponent } from './components/terminos-y-condiciones/terminos-y-condiciones.component';
import { InfoNoticiasComponent } from './components/info-noticias/info-noticias.component';
import { PagesComponent } from './pages/pages.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent, data : { titulo: 'Descuentos Mdicos'} },
  { path: 'registro', component: RegistroComponent },
  { path: 'terminos-y-condiciones', component: TerminosYCondicionesComponent },
  { path: 'info-noticias', component: InfoNoticiasComponent },
  {path: '', component: PagesComponent,
  loadChildren: () => import('./pages/pages.module').then(mod => mod.PagesModule)
  // loadChildren: './pages/pages.module#PagesModule'
},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true, preloadingStrategy: NoPreloading})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
