
/**
   * Componente calendario
   * 
   */
  import { Component, OnInit, ChangeDetectorRef, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';
  import { CalendarEvent,CalendarMonthViewDay, CalendarView, CalendarMonthViewBeforeRenderEvent } from 'angular-calendar';
  import { HttpClient } from '@angular/common/http';
  import { startOfYear } from 'date-fns';
  import { registerLocaleData } from '@angular/common';
  import { environment } from '../../../environments/environment.prod';
  import { WeekViewHourColumn } from 'calendar-utils';
  import { SucursalesService } from '../../services/sucursales.service';
  import { UserService } from 'src/app/services/user.service';
  import { InfoMedico } from 'src/app/interfaces/medicos';
  import { InfoServicio } from 'src/app/interfaces/servicios';
  import { AplicationService } from '../../services/aplication.service';

  
  export class AppModule { }
  
  
  import localEs from '@angular/common/locales/es';
  import { SettingsService } from 'src/app/services/settings.service';
  import { PermisosService } from 'src/app/services/permisos.service';
  import { FormControl, FormGroup, Validators } from '@angular/forms';
  
  registerLocaleData(localEs);
  declare function init_plugins();
  declare function init_appInit();
  
  
  /**
   * Interfaz para dias festivos.
   * 
   */
  interface Holiday {
    date: string;
    name: string;
  
  }
  
  type CalendarEventWithMeta = CalendarEvent<{ type: 'holiday'; festivo: Holiday; } | { type: 'normal' }>;
  // type CalendarEventWithMeta = CalendarEvent<any>;
  
  @Component({
    selector: 'app-calendario',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './calendario.component.html',
    styleUrls: ['./style.css']
  })
  export class CalendarioComponent implements OnInit {
  
    @ViewChild('selectService') selectService: ElementRef;
    @ViewChild('selectDoctor') selectDoctor: ElementRef;
    @ViewChild('selectFiltro') selectFiltro: ElementRef;

    @ViewChild('selectFiltroMedicos') selectFiltroMedicos: ElementRef;
    @ViewChild('selectServiceMedicos') selectServiceMedicos: ElementRef; 

    public serviciosActivos: InfoServicio[];
    public serviciosActivosUserMedic: InfoServicio[];
    public serviciosActivosAsociadoMedicos: InfoServicio[];
    public medicosActivos: InfoMedico[];
  
  
    view: CalendarView = CalendarView.Month;
    CalendarView = CalendarView;
    renderEvent: CalendarMonthViewBeforeRenderEvent;
    selectedMonthViewDay: CalendarMonthViewDay;
    hourColumns: WeekViewHourColumn[];
    events: CalendarEventWithMeta[] = [];
  
    viewDate = startOfYear(new Date());
    locale: string = 'es';
    public user = JSON.parse(this.userService.getLocalStorage('user'));
    holidays;
    hist;
    load = false;
    register = false;
    idServicio;
    dataSucursal;
    idMedico;
    filtro;
    filtroMedicos;
    idServicioMedicos;

    showModalBox: boolean = false;

    // Variable para visualizar info de una cita
    public infoCita;
    public apiUrl = environment.apiUrl;
    public reprogramar: string;
    public formReprogramar: FormGroup;
    public horarios;


    // public fechaReprogramacion = new FormControl();


    constructor(
      private http: HttpClient,
      private sucursalService: SucursalesService,
      public appService: AplicationService,
      private userService: UserService,
      private cdr: ChangeDetectorRef,
      private settingsService: SettingsService,
      private permisos: PermisosService) { }
  
    ngOnInit(): void {


  
      // document.getElementById('btn_hoy-calendar').click();
      init_plugins();
      init_appInit();
      
      this.setTimeLoad();
        this.fetchHolidays();
        this.user = JSON.parse(this.userService.getLocalStorage('user'));
        if(this.user.tipoCuenta == "empresa" || this.user.tipoCuenta === 'empleado'){
          this.getServiciosActivosSucursal();

          this.settingsService.actualizarInfo.subscribe( () => {
            this.user = JSON.parse(this.userService.getLocalStorage('user'));
            this.getServiciosActivosSucursal();
          });
        } 
        
        if(this.user.tipoCuenta == "medico"){
            this.getServiciosActivos();
        }

    }
  
  /////////////////////////////////////////////SECCION FESTIVOS//////////////////////////////////////////////////////////////////



    /**
     * Metodo fetch para cargar y setear los dias festivos mediante la apiURL
     * @return fecha y descripcion del dia festivo.
     */
    private async fetchHolidays() {
      let temp = await this.http.get(this.apiUrl + '/info/festivos/47').toPromise();
  
      this.holidays = temp['body'].map((festivo) => {
        return {
          start: new Date(festivo.date),
          title: 
          `<h5 class="text-info">Festivo:</h5><h6 class="text-white">   ${festivo.name}</h6>
          `,
          locale: 'es',
          cssClass: 'festivo',
          resizable: {
            beforeStart: true,
          },
          meta: {
            type: 'holiday',
            festivo,
          },
        };
      });
  
      if (this.holidays) {
        this.events = this.holidays;
        this.cdr.markForCheck();
      }
    }


/////////////////////////////////////////////SECCION MEDICOS//////////////////////////////////////////////////////////////////



      /**
     * Metodo que filtra las citas teniendo encuenta el servicio.
     */
    selectedServiceMedicos() {
      this.events = [];
      this.events = [...this.holidays];
      
      this.idServicioMedicos = this.selectServiceMedicos.nativeElement.value;
      this.dataSucursal = JSON.parse(localStorage.getItem('user'));
      this.selectedFilterMedicos();
      this.setTimeLoad();
      }

    async selectedFilterMedicos() {

      this.events = [];
      this.events = [...this.holidays];
        //Citas con Filtro por servicio
        let tm1 = await this.getCitasMedicos();
        this.updateDataCalendar([...tm1]);     
    }
    async getCitasMedicos() {
  
      this.setTimeLoad();
      let temp = await this.http.get(this.apiUrl + `/citasactimedi/${this.user.idMedico}/${this.idServicioMedicos}`).toPromise();
      if (temp) this.register = true;

      let estructuraCitas = this.setEstructuraCitas(temp['body'], 'citaActiva');
      return estructuraCitas;
    }

    /**
    * Metodo que permite obtener los servicios activos
    */
   getServiciosActivos() {
    this.sucursalService.getServiciosMedicos({ idMedico:this.user.idMedico})
      .subscribe(res => {this.serviciosActivosUserMedic = res});
      
  }

/////////////////////////////////////////////SECCION EMPRESAS//////////////////////////////////////////////////////////////////





    /**
     * Metodo que filtra las citas teniendo encuenta el servicio.
     */
    selectedService() {
      this.events = [];
      this.events = [...this.holidays];
      
      this.idServicio = this.selectService.nativeElement.value;
      this.dataSucursal = JSON.parse(localStorage.getItem('user'));
      this.getMedicosActivosSucursal(this.idServicio);
      this.setTimeLoad();

      }

    async selectedFilterWithoutDoctor() {

      this.filtro = this.selectFiltro.nativeElement.value;
      this.events = [];
      this.events = [...this.holidays];

      if (this.filtro == 'citas') this.updateDataCalendar(await this.getCitasActivasSinMedico());
      if (this.filtro == 'historial') this.updateDataCalendar(await this.getHistorialCitasSinMedico());
      if (this.filtro == 'todos') {
        //Citas con Filtro por servicio
        let tm1 = await this.getCitasActivasSinMedico();
        let tm2 = await this.getHistorialCitasSinMedico();

        this.updateDataCalendar([...tm1, ...tm2]);
      }


      if(this.events.length <= 27 ){    
        this.advertPerfil();
      }

    }
  
  
    /**
  * Metodo que filtra las citas teniendo encuenta el medico.
  */
    async selectedDoctor() {
      this.filtro = this.selectFiltro.nativeElement.value;
      this.events = [];
      this.events = [...this.holidays];
  
      if (this.filtro == 'citas') 
      this.updateDataCalendar(await this.getCitasActivasConMedico());

      if (this.filtro == 'historial') 
      this.updateDataCalendar(await this.getHistorialCitasConMedico());
      if (this.filtro == 'todos' ) {
        //Citas con Filtro por servicio
        let tm1 = await this.getCitasActivasConMedico();
        let tm2 = await this.getHistorialCitasConMedico();
  

        this.updateDataCalendar([...tm1, ...tm2]);
      }

    
    }
  
  
    async getCitasActivasSinMedico() {

      this.setTimeLoad();
      let temp = await this.http.get(this.apiUrl + `/citasactivassucu/${this.dataSucursal['idSucursales']}/${this.idServicio}/0`).toPromise();

      // console.log(temp);
      if (temp) this.register = true;
  
  
      let estructuraCitas = this.setEstructuraCitas(temp['body'], 'citaActiva');
      

      return estructuraCitas;
    }

    async getCitasActivasConMedico() {

      this.setTimeLoad();
      this.idMedico = this.selectDoctor.nativeElement.value;
      let temp = await this.http.get(this.apiUrl + `/citasactivassucu/${this.dataSucursal['idSucursales']}/${this.idServicio}/${this.idMedico}`).toPromise();
      if (temp) this.register = true;
      
  
      let estructuraCitas = this.setEstructuraCitas(temp['body'], 'citaActiva');
      return estructuraCitas;
    }
  
    async getHistorialCitasSinMedico() {
  
      this.setTimeLoad();
      let temp = await this.http.get(this.apiUrl + `/historialweb/${this.dataSucursal['idSucursales']}/${this.idServicio}/0`).toPromise();
      if (temp) this.register = true;

      let estructuraHistorialCitas = this.historialCitas(temp['body']);

      return estructuraHistorialCitas;
    }

    async getHistorialCitasConMedico() {

      this.setTimeLoad();
      this.idMedico = this.selectDoctor.nativeElement.value;
      let temp = await this.http.get(this.apiUrl + `/historialweb/${this.dataSucursal['idSucursales']}/${this.idServicio}/${this.idMedico}`).toPromise();
      if (temp) this.register = true;

      let estructuraHistorialCitas = this.historialCitas(temp['body']);

      return estructuraHistorialCitas;
    }


    setTimeLoad() {
      this.load = true;
      setTimeout(() => {
        this.load = false,
          this.register = true;
      }, 500);
    }

    /**
     * Mtodo para seteear los eventos del calendario
     * @param eventList Estructura con lista para settear en el calendario
     */
    updateDataCalendar(eventList) {
      this.events = [];
      this.events = [...this.holidays, ...eventList];
      this.cdr.markForCheck();
    }

    /**
     * Metodo que setea las citas en la agenda (calendario) con sus respectivos datos.
     * @return citas con sus datos.
     */
    setEstructuraCitas(listaCitas: Array<any>, type?) {

 
      let now = new Date().getDate();
      let estructuraCitas = [];
      // console.log(listaCitas);

      listaCitas.forEach(cita => {
        let fCita = new Date(cita['fecha']).getDate();
        let finalDate = `${cita['fecha'].split('T')[0]}, ${cita['hora'].split(' ')[0]}:00:00`
        estructuraCitas.push({
          start: new Date(finalDate),
          title:
            `
            <h5 class="text-info">Servicio:</h5><h6 class="text-white">   ${cita['nombreServicio']|| 'No registra'}</h6>
            <h5 class="text-info">Paciente:</h5><h6 class="text-white">  ${cita['nombres']|| 'No registra'} ${cita['apellidos']|| 'No registra'}</h6>
            <h5 class="text-info">Telfono:</h5><h6 class="text-white">  ${cita['telefono']|| 'No registra'}</h5>
            <h5 class="text-info">Direccin:</h5><h6 class="text-white">  ${cita['direccion']|| 'No registra'} </h6>
            <h5 class="text-info">Barrio:</h5><h6 class="text-white">  ${cita['barrio']|| 'No registra'}</h6>
            <h5 class="text-info">Hora:</h5><h6 class="text-white">  ${cita['hora']|| 'No registra'}</h6>
            <h5 class="text-info">Medico:</h5><h6 class="text-white">  ${cita['nombresMedico']|| 'No registra'} ${cita['apellidosMedico']|| 'No registra'}</h6>
            `,
          cssClass: fCita === now ? 'citaActual' : type,
          // cssClass: this.setClassCitas(new Date(cita['fecha'])),
          resizable: {
            beforeStart: true,
            servicio: cita['nombreServicio'] || 'No registra',
            paciente: cita['nombres']|| 'No registra' + ' ' + cita['apellidos']|| 'No registra',
            telefono: cita['telefono']|| 'No registra',
            direccion: cita['direccion']|| 'No registra',
            barrio: cita['barrio']|| 'No registra',
            hora: cita['hora']|| 'No registra' ,
            medico: cita['nombresMedico']|| 'No registra' + ' ' + cita['apellidosMedico']|| 'No registra',
            avatarUser: cita['avatarUser'] || 'avatarundefined.png',
            numeroDocumento: cita['numeroDocumento']|| 'No registra',
            fechaNacimiento: cita['fechaNacimiento']|| 'No registra',
            idCitasmedicasactivas: cita['idCitasmedicasactivas'],
            idMembers: cita['idMembers']

          }
          
        });

      });
      return estructuraCitas;
    }

    /**
     * Metodo fetch para cargar y setear los dias festivos mediante la apiURL
     * @return fecha y descripcion del dia festivo.
     */
    historialCitas(listaHistorial: Array<any>) {

      let estructuraHistorial = [];
      listaHistorial.forEach(cita => {
        let finalDate = `${cita['fecha'].split('T')[0]}, ${cita['hora'].split(' ')[0]}:00:00`;
        estructuraHistorial.push({
          start: new Date(finalDate),
          title:
          `
          <h5 class="text-info">Servicio:</h5><h6 class="text-white">  ${cita['nombreServicio']|| 'No registra'}</h6>
          <h5 class="text-info">Paciente:</h5><h6 class="text-white">  ${cita['nombres']|| 'No registra'} ${cita['apellidos']|| 'No registra'}</h6>
          <h5 class="text-info">Hora:</h5><h6 class="text-white">  ${cita['hora']|| 'No registra'}</h6>
          <h5 class="text-info">Telfono:</h5><h6 class="text-white">  ${cita['telefono']|| 'No registra'}</h6>
          <h5 class="text-info">Direccin:</h5><h6 class="text-white">  ${cita['direccion']|| 'No registra'} </h6>
          <h5 class="text-info">Barrio:</h5><h6 class="text-white">  ${cita['barrio']|| 'No registra'}</h6>
          <h5 class="text-info">Medico: </h5><h6 class="text-white"> ${cita['nombresMedico']|| 'No registra'} ${cita['apellidosMedico']|| 'No registra'}</h6>
          <h5 class="text-info">Estado:</h5><h6 class="text-white">  Citas pasadas, canceladas o en historial.</h6>`,
          cssClass: 'citaPasada',
          resizable: {
            beforeStart: true,
            servicio: cita['nombreServicio'] || 'No registra',
            paciente: cita['nombres']|| 'No registra' + ' ' + cita['apellidos']|| 'No registra',
            telefono: cita['telefono']|| 'No registra',
            direccion: cita['direccion']|| 'No registra',
            barrio: cita['barrio']|| 'No registra',
            hora: cita['hora']|| 'No registra' ,
            medico: cita['nombresMedico']|| 'No registra' + ' ' + cita['apellidosMedico']|| 'No registra',
            numeroDocumento: cita['numeroDocumento']|| 'No registra',
            fechaNacimiento: cita['fechaNacimiento']|| 'No registra',

            avatarUser: cita['avatarUser'] || 'avatarundefined.png'
          }
        });

      });

      return estructuraHistorial;
    }


    /**
    * Metodo que permite obtener los servicios activos
    */
    getServiciosActivosSucursal() {
      this.sucursalService.getServiciosActivosSucursal({ idMunicipio: this.user.idmunicipioSucursal, idSucursal: this.user.idSucursales })
        .subscribe(res => this.serviciosActivos = res );
    }

    
    /**
  * Metodo que permite obtener los medicos activos
  */
    getMedicosActivosSucursal(idServicio) {

      this.sucursalService.getMedicosActivosServicio({ idServicio: idServicio, idMunicipio: this.user.idmunicipio, idSucursal: this.user.idSucursales })
        .subscribe(res => this.medicosActivos = res);
    }
  
  
    dayClicked(dia) {
      if (dia.day.isPast === true) {
      } else {
        this.viewDate = dia.day.date;
        this.view = CalendarView.Day;
  
      }
    }
  
    // handleEvent(action: string, event: CalendarEvent): void {
    // }
  
    hourSegmentClicked(ev) {
      let date = ev.date.toString();
      date = date.split(' ');
      date = date[0];

    }

    advertPerfil(){
      this.appService.swalConfirm('Sin citas', 'Debes tener por lo menos una cita activa para visualizar la agenda y usar los filtros. Deseas sacar una cita?', 'info')
      .then(()=>{
        this.appService.redireccion('IPS','citas' );
      });
    } 


    eventClicked(ev) {

      this.reprogramar = 'sin_reprogramar';
      if(ev.event.cssClass === 'festivo') return;
      this.infoCita = ev;
      document.getElementById('btn-citas-activas-modal').click();
    }


    cancelarCita() {
  
      this.appService.swalConfirm('', ' Esta seguro que desea eliminar esta cita ?', 'error').then(()=>{
   
          if( this.permisos.permisos(0, 0, 0, 'eliminar')) {

            // console.log(this.filtro);

            // console.log({idCitaEnCurso: this.infoCita.event.resizable.idCitasmedicasactivas , motivo: 'Cita cancelada por el personal de la empresa.', tabla: 'activa', cancelada: 1 });

          this.sucursalService.postCancelarCita({idCitaEnCurso: this.infoCita.event.resizable.idCitasmedicasactivas , motivo: 'Cita cancelada por el personal de la empresa.', tabla: 'activa', cancelada: 1 }).subscribe( () => {
         
            if (this.dataSucursal['idSucursales'] && this.idServicio && this.idMedico) {
              // console.log('citas con medico');

              this.selectedDoctor();
            } else {
              // console.log('citas sin medico');
              this.selectedFilterWithoutDoctor();
            }
         
            document.getElementById('btn-cerrar-modal-infoCita').click(); 
          });
        }
      });
    }


    InicializarFormReprogramar() {
      
      this.formReprogramar = new FormGroup ({
          fecha : new FormControl ('', Validators.required),
          servicio: new FormControl ('', Validators.required),
          medico: new FormControl ('medico', Validators.required)

      });
    }

    buscarHorario(){
        // console.log('aquii', this.formReprogramar);

        if (this.formReprogramar.get('fecha').invalid) {
          this.appService.swal('Advertencia', 'Por favor escoge una fecha para la cita.', 'warning');
          return;
        }
    
        if ((this.formReprogramar.get('servicio').value === 'servicio' )) {
          this.appService.swal('Advertencia', 'Por favor escoge un servicio.', 'warning');
          return;
        }
    
        if (this.formReprogramar.get('medico').value === 'medico') {
          this.appService.swal('Advertencia', 'Por favor escoge un mdico..', 'warning');
          return;
        }
    
        this.sucursalService.getHorario({ fecha: this.formReprogramar.get('fecha').value,
                                          idSucursal : this.user.idSucursales,
                                          idServicio : this.formReprogramar.get('servicio').value,
                                          idMedico : this.formReprogramar.get('medico').value })
                            .subscribe( res => {this.horarios = res ;
                                                // console.log(res);
                                                document.getElementById('select-medico').className = 'form-control';
                                              });
    
    }


      // Metodo para reprogramar una cita, recibe como parametros la hora en la que se desea sacar la cita, el
  // id del consultorio y el id de horario.
  updateCita(hora, idConsultorios, idHorario) {

    // Validacion de edicion
    if(this.permisos.permisos(0, 0, 0, 'editar')) {


      if (!this.formReprogramar.get('fecha').value ||
        !this.formReprogramar.get('servicio').value ||
        ( this.formReprogramar.get('medico').value === 'medico' ) ) {
          this.appService.swal('Advertencia', 'Por favor completa, la fecha el servicio y el mdico para reprogramar la cita.', 'warning');
          return;
      }


    let info = {
        idCitasmedicasactivas: this.infoCita.event.resizable.idCitasmedicasactivas,
        fecha: this.formReprogramar.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
        hora,
        quienSacoCita : this.user.idMembers,
        idServicios : this.formReprogramar.get('servicio').value,
        idConsultorios,
        _idHorario: idHorario,
        idSucursales: this.user.idSucursales,
        idMedico : this.formReprogramar.get('medico').value,
        paciente : this.infoCita.event.resizable.idMembers
    };

    let titulo = ` Estas seguro que deseas reprogramar la cita para el ${this.formReprogramar.get('fecha').value}, a las ${hora} ?`;

    this.appService.swalConfirm('Reprogramar cita', titulo, 'warning').then(() => {
      this.sucursalService.putReprogramarCita(info).subscribe( () => {
        document.getElementById('btn-cerrar-modal-infoCita').click();
        location.reload();
        });
      });
      
    }
   }

  }


