import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router } from '@angular/router';
import { UserService } from '../user.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {

  constructor(private userService: UserService,
              public router: Router) {}

  canActivate( route: ActivatedRouteSnapshot ) {

    let info = JSON.parse(this.userService.getLocalStorage('user'));

    // console.log(info.tipoEmpresa);
    // console.log(route.routeConfig.path);
    if (info) {

      switch (info.tipoCuenta) {
      case 'empresa':

        if (info.tipoEmpresa === 'Consultorio' && route.routeConfig.path === 'consultorio') {
          return true;
        } else if (info.tipoEmpresa === 'IPS' && route.routeConfig.path === 'ips') {
          return true;
        } else if (info.tipoEmpresa === 'Veterinaria' && route.routeConfig.path === 'veterinaria') {
          return true;
        } else {
          this.router.navigate(['']);
          return false;
        }

        case 'empleado' :

        if (info.tipoCuenta === 'empleado' && route.routeConfig.path === 'empleado') {
            return true;
        } else {
          this.router.navigate(['']);
          return false;
        }


        case 'medico' :

          if (info.tipoCuenta === 'medico' && route.routeConfig.path === 'medico') {
              return true;
          } else {
            this.router.navigate(['']);
            return false;
          }
      }
    } else {
      this.router.navigate(['/login']);
      return false;
    }
  }

}
