import { Component, OnInit } from '@angular/core';
import { SucursalesService } from '../../services/sucursales.service';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from 'src/app/services/user.service';
import { InfoCitas, InfoCitasEnCurso } from 'src/app/interfaces/citas';
import { environment } from 'src/environments/environment.prod';
import * as moment from 'moment';
import { AplicationService } from 'src/app/services/aplication.service';
import Swal from 'sweetalert2';
import { InfoServicio } from '../../interfaces/servicios';
import { InfoMedico } from 'src/app/interfaces/medicos';
import { TipoDocumento } from 'src/app/interfaces/info';
import { PermisosService } from 'src/app/services/permisos.service';
import { SettingsService } from 'src/app/services/settings.service';
import { MedicosService } from 'src/app/services/medicos.service';

@Component({
  selector: 'app-citas',
  templateUrl: './citas.component.html'
})
export class CitasComponent implements OnInit {

  public empresa;
  public citas: InfoCitas[];
  public citasEnCurso: InfoCitasEnCurso[];
  public apiUrl = environment.apiUrl;
  public hoyInput;
  public hoy;
  public numeroDocumento;
  public serviciosActivos: InfoServicio[];
  public medicosActivos: InfoMedico[];
  public tipoDocumento: TipoDocumento[];

  //////// sacar cita
  public existe: string;
  public reprogramar  = 'sin_reprogramar';
  public citaReprogramar: InfoCitas;
  public formMemberExiste: FormGroup;
  public formMemberNoExiste: FormGroup;
  public horarios;
  public paciente;
  public intervalo;

  load = false;
  register = false;

  constructor(private sucursalService: SucursalesService,
              private userService: UserService,
              public appService: AplicationService,
              private permisos: PermisosService,
              private settingsService: SettingsService,
              private medicoService: MedicosService) { }

  ngOnInit(): void {

    this.setTimeLoad();
    // console.log(this.appService.hoy('suma', 'months', 3));
    this.hoy = this.appService.hoy('hoy', '', 0);
    this.empresa = JSON.parse(this.userService.getLocalStorage('user'));

      this.hoy = this.appService.hoy('hoy', '', 0);
      this.getCitasEnCurso();
      this.inicializarFormularioPaciente();
      this.inicializarFormulario();

      if(this.empresa.tipoCuenta === 'medico') {

        this.intervalo =  setInterval(() => {
          this.getCitasEnCurso();
           }, 30000);
      }


    this.settingsService.actualizarInfo.subscribe( () => {
      this.getInfoUser();
      this.getCitasEnCurso();
  });

  }

  ngOnDestroy(): void {
    // Called once, before the instance is destroyed.
    // Add 'implements OnDestroy' to the class.
    clearInterval(this.intervalo);
  }


  getInfoUser() {
    this.empresa = JSON.parse(this.userService.getLocalStorage('user'));

  }

  getCitasActivasPorDocumento(formBuscar: NgForm) {
 
    let info = {numeroDocumento: formBuscar.value.numeroDocumento, idSucursal: this.empresa.idSucursales};
    this.sucursalService.getCitasDocumentoUser(info).subscribe( (res: any) => {

      // console.log(res);
      this.numeroDocumento = formBuscar.value.numeroDocumento;
      this.horarios = undefined;
      this.citas = res;
      this.reprogramar = 'sin_reprogramar';

      if (res.length <= 0) {
        this.existe = undefined;
        document.getElementById('btn-sin-citas').click();
        return;
      }

      document.getElementById('btn-citas-activas').click();
    });
  }

  cambioModal () {
    this.buscarMember();
    document.getElementById('btn-sin-citas').click();
  }


  agregarCita(idCitaActica) {
    this.sucursalService.postActivarCita(idCitaActica).subscribe( () => {
      this.getCitasEnCurso();
      document.getElementById('btn-cerrar-modalcitasActi').click();
      document.getElementById('btn-cerrar-modalcitas').click();
    });
  }

  getCitasEnCurso(){

    if(this.empresa.tipoCuenta === 'medico') {
      this.medicoService.getCitasCursoMedico(this.empresa.idMedico).subscribe( res => this.citasEnCurso = res );
    } else {


      if( ( this.empresa.tipoCuenta === 'empleado' && this.empresa.validada === 0 ) || !this.empresa.idSucursales ) {
          return;
      }

      this.sucursalService.getCitasEnCurso(this.empresa.idSucursales).subscribe( res => this.citasEnCurso = res );
    }
  }

  cambiarEstado(idCitaEnCurso) {
    this.sucursalService.getCambiarEstadoCita(idCitaEnCurso).subscribe(() => this.getCitasEnCurso());
  }

  finalizarCitaAlerta(idCitaEnCurso) {
    this.swalConfirm('Finalizar cita', ' Estas seguro que deseas finalizar la cita ?', 'warning', 'finalizar', idCitaEnCurso, '');
  }

  finalizarCita(idCitaEnCurso) {
    this.sucursalService.getFinalizarCita(idCitaEnCurso).subscribe(() => this.getCitasEnCurso() );
  }

  cancelarCitaAlerta(idCitaEnCurso, tabla) {

    // console.log(idCitaEnCurso, tabla);
    this.swalConfirm('Cancelar cita', ' Estas seguro que deseas cancelar la cita ?', 'warning', 'cancelar', idCitaEnCurso, tabla);
  }

  cancelarCita(idCitaEnCurso, motivo, tabla, cancelada) {


    if( this.permisos.permisos(0, 0, 0, 'eliminar')) {
        // console.log(idCitaEnCurso, motivo, tabla, cancelada);
      this.sucursalService.postCancelarCita({idCitaEnCurso , motivo, tabla, cancelada}).subscribe( () => {
      this.getCitasEnCurso();
      document.getElementById('btn-cerrar-modalcitasActi').click();
    });
    }
   

  
  }

  swalConfirm(titulo: string, texto: string, icono, accion: string, idCita, tabla: string) {

    Swal.fire({
      title: titulo,
      text: texto,
      icon: icono,
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      cancelButtonText: 'Cancelar',
    }).then((result) => {
      if (result.isConfirmed) {

        if (accion === 'finalizar') {
          this.finalizarCita(idCita);
          return;
        }

        this.cancelarCita(idCita, 'Cita cancelada por el personal de la empresa.', tabla, 1);

      }
    });
  }


  // Metodo para buscar un usuario a traves de su numero de documento
  buscarMember() {

    // console.log(document.getElementById('select-servicio'));
    this.formMemberNoExiste.reset();
    this.formMemberExiste.reset();

    this.sucursalService.getMemberNumeroIdentificacion(this.numeroDocumento).subscribe( (res: any) => {
        
      // Validacion que el numero de documento no pertenesca a una cuenta empresarial.
      if (res.existe && res.body.tipoCuenta === 'empresa') {

          let mensaje = `No se puede sacar cita, el nmero de documento : ${this.numeroDocumento}, pertenece a una cuenta empresarial.`;
          this.appService.swal('', mensaje, 'warning');
          return;
      }

      // Si el numero de documento esta registrado.
      if (res.existe) {
        this.paciente = res.body;
        this.inicializarFormulario();
        this.existe = 'existe';
        this.getServiciosActivosSucursal();
        return;
      }

      // Si el numeroDocumento no se encuentra registrado.
      this.inicializarFormularioPaciente();
      this.inicializarFormulario();
      this.existe = 'no_user';
      this.getServiciosActivosSucursal();
      this.getTiposDocumento();
    });
  }

  // Formulario de datos necesarios para sacar una cita.
  inicializarFormulario() {

    this.formMemberExiste = new FormGroup({
      servicio : new FormControl( 'servicio', Validators.required  ),
      fecha : new FormControl( null, Validators.required  ),
      medico : new FormControl( 'medico', Validators.required  ),
    });
  }

  // Formulario si el paciente no existe.
  inicializarFormularioPaciente() {
    this.formMemberNoExiste = new FormGroup({
      nombres : new FormControl( null, Validators.required),
      apellidos : new FormControl( null, Validators.required),
      correo : new FormControl( null, [Validators.email]),
      telefono : new FormControl( null, Validators.required),
      tipoDocumento : new FormControl( 'tipo_documento', Validators.required),
      numeroDocumento : new FormControl( this.numeroDocumento, Validators.required),
      fechaNacimiento : new FormControl( null, Validators.required),
    });
  }

  // Metodo para llama los servicios activos que tiene una sucursal para sacar cita.
  getServiciosActivosSucursal() {

    this.formMemberExiste.get('servicio').setValue('servicio');

    this.sucursalService.getServiciosActivosSucursal({idMunicipio: this.empresa.idmunicipio, idSucursal: this.empresa.idSucursales})
    .subscribe( res => {
      this.serviciosActivos = res;
      // console.log( this.formMemberExiste);
      // document.getElementById('select-servicio').click();
    });
  }

  // Metodo para obtener los medicos activos segun el servicio.
  getMedicosActivosSucursal(ev) {
    this.horarios = undefined;
    // this.formMemberExiste.get('medico').setValue('medico');
    // tslint:disable-next-line: max-line-length
    this.sucursalService.getMedicosActivosServicio({idServicio: ev.target.value, idMunicipio: this.empresa.idmunicipio, idSucursal: this.empresa.idSucursales})
                        .subscribe( res => { 
                          // console.log(res);
                          this.medicosActivos = res;
                          this.formMemberExiste.get('medico').setValue('medico');
                          // this.buscarHorario();
                        });
  }

  getTiposDocumento() {
    this.appService.getTiposDocumento().subscribe( res => this.tipoDocumento = res );
  }


  // Metodo para buscar un horario de atencin para sacar cita.
  buscarHorario() {

    // console.log(this.formMemberExiste.get('fecha').value,
    // this.formMemberExiste.get('servicio').value,
    // this.formMemberExiste.get('medico').value);

    // console.log(this.formMemberExiste);

    // console.log(this.formMemberExiste.get('fecha').invalid,
    // this.formMemberExiste.get('servicio').value,
    // this.formMemberExiste.get('medico').value);

    if (this.formMemberExiste.get('fecha').invalid) {
      this.appService.swal('Advertencia', 'Por favor escoge una fecha para la cita.', 'warning');
      return;
    }

    if ((this.formMemberExiste.get('servicio').value === 'servicio' )) {
      this.appService.swal('Advertencia', 'Por favor escoge un servicio.', 'warning');
      return;
    }

    if (this.formMemberExiste.get('medico').value === 'medico') {
      this.appService.swal('Advertencia', 'Por favor escoge un mdico..', 'warning');
      return;
    }

    this.sucursalService.getHorario({ fecha: this.formMemberExiste.get('fecha').value,
                                      idSucursal : this.empresa.idSucursales,
                                      idServicio : this.formMemberExiste.get('servicio').value,
                                      idMedico : this.formMemberExiste.get('medico').value })
                        .subscribe( res => {this.horarios = res ;
                                            // console.log(res);
                                            document.getElementById('select-medico').className = 'form-control';
                                          });

  }

  // Iniciar metodos necesarios para reprogramar una cita.
  reprogramarCita(cita: InfoCitas) {

    this.inicializarFormulario();
    this.getServiciosActivosSucursal();
    this.getMedicosActivosSucursal({target : { value : cita.idServicios}});
    this.formMemberExiste.get('servicio').setValue(cita.idServicios);
    this.reprogramar = 'reprogramar';
    this.citaReprogramar = cita;

  }


  // Metodo para reprogramar una cita, recibe como parametros la hora en la que se desea sacar la cita, el
  // id del consultorio y el id de horario.
  updateCita(hora, idConsultorios, idHorario) {

    // Validacion de edicion
    if(this.permisos.permisos(0, 0, 0, 'editar')) {


      if (!this.formMemberExiste.get('fecha').value ||
        !this.formMemberExiste.get('servicio').value ||
        ( this.formMemberExiste.get('medico').value === 'medico' ) ) {
          this.appService.swal('Advertencia', 'Por favor completa, la fecha el servicio y el mdico para reprogramar la cita.', 'warning');
          return;
      }


    let info = {
        idCitasmedicasactivas: this.citaReprogramar.idCitasmedicasactivas,
        fecha: this.formMemberExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
        hora,
        quienSacoCita : this.empresa.idMembers,
        idServicios : this.formMemberExiste.get('servicio').value,
        idConsultorios,
        _idHorario: idHorario,
        idSucursales: this.empresa.idSucursales,
        idMedico : this.formMemberExiste.get('medico').value,
        paciente : this.citaReprogramar.paciente
    };


    let titulo = ` Estas seguro que deseas reprogramar la cita para el ${this.formMemberExiste.get('fecha').value}, a las ${hora} ?`;

    this.appService.swalConfirm('Reprogramar cita', titulo, 'warning').then(() => {
      this.sucursalService.putReprogramarCita(info).subscribe( () => {
        document.getElementById('btn-cerrar-modalcitas').click();
        location.reload();
      });
    }
    );
      
    }

  
  }


  // Metodo para sacar una cita, recibe como parametros la hora en la que se desea sacar la cita, el id del consultorio y el id de horario.
  sacarCita(hora, idConsultorios, idHorario) {

    // Validar si tiene permisos
    if(this.permisos.permisos(0, 0, 0, 'crear')) { 

      // Validacion que la hora escogida para la cita no haya pasado.
    if (this.appService.hoy('hoy', '', 0) === this.formMemberExiste.get('fecha').value) {
      let h = hora.split(' ');
      if ( parseInt(moment(new Date()).format('HH'))  > parseInt(h[0]) ) {

        this.appService.swal('Advertencia', 'La hora seleccionada ya paso, por favor escoge otro horario de atencin.', 'warning');
        return;
      }

    }


    let info = {};

    // Si el usuario al que se quiere sacar la cita no existe, se crea una cuenta de usuario con los datos basicos y se saca una cita.
    if  (this.existe === 'no_user') {

      // Validaciones
      if (!this.formMemberNoExiste.get('nombres').value ||
      !this.formMemberNoExiste.get('apellidos').value ||
      !this.formMemberNoExiste.get('telefono').value ||
      this.formMemberNoExiste.get('tipoDocumento').value === 'tipo_documento' ||
      !this.formMemberNoExiste.get('numeroDocumento').value ||
      !this.formMemberNoExiste.get('fechaNacimiento').value) {

        this.appService.swal('Advertencia', 'Por favor completa los datos del paciente requeridos (*)', 'warning');
        return;

      }

      if (this.formMemberNoExiste.get('correo').value) {
        if (this.formMemberNoExiste.get('correo').getError('email')) {
          this.appService.swal('Advertencia', 'El correo es invalido.', 'warning');
          return;
        }
      }

      // console.log(this.formMemberNoExiste);
      info = {
        existe : false,
        cita : {
          fecha: this.formMemberExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
          hora,
          quienSacoCita : this.empresa.idMembers,
          idServicios : this.formMemberExiste.get('servicio').value,
          idConsultorios,
          _idHorario: idHorario,
          idSucursales: this.empresa.idSucursales,
          idMedico : this.formMemberExiste.get('medico').value
        },
        usuario : {
          nombres : this.formMemberNoExiste.get('nombres').value,
          apellidos : this.formMemberNoExiste.get('apellidos').value,
          correo : this.formMemberNoExiste.get('correo').value,
          tipoCuenta : 'usuario',
          telefono : this.formMemberNoExiste.get('telefono').value,
          idtipoDocumentos: this.formMemberNoExiste.get('tipoDocumento').value,
          numeroDocumentoUser : this.formMemberNoExiste.get('numeroDocumento').value,
          fechaNacimiento : this.formMemberNoExiste.get('fechaNacimiento').value
        }
      };
    } else {


      // Si el usuario al que se desea sacar la cita ya existe.
      info = {
        fecha: this.formMemberExiste.get('fecha').value  + ' ' + hora.split(' ')[0] + ':00:00',
        hora,
        quienSacoCita : this.empresa.idMembers,
        idServicios : this.formMemberExiste.get('servicio').value,
        paciente : this.paciente.idMembers,
        idConsultorios,
        _idHorario: idHorario,
        idSucursales: this.empresa.idSucursales,
        idMedico : this.formMemberExiste.get('medico').value,
        existe : true
    };
  }

    // console.log(info);
    let titulo = ` Estas seguro que deseas programar la cita para el ${this.formMemberExiste.get('fecha').value}, a las ${hora} ?`;

    // Llamado al metodo postSacarCitaSucu para sacar una cita.
    this.appService.swalConfirm('Programar cita', titulo, 'warning').then(() => {
      this.sucursalService.postSacarCitaSucu(info).subscribe( () => {
        // console.log('aquii');

        setTimeout(() => {
          location.reload();
        }, 900);
        
      });
    }
  );

  }
    // console.log(info);
  }



  setTimeLoad() {
    this.load = true;
    setTimeout(() => {
      this.load = false,
        this.register = true;
    }, 500);
  }
}
