import { Injectable } from '@angular/core';
import { AplicationService } from './aplication.service';
import { UserService } from './user.service';

@Injectable({
  providedIn: 'root'
})
export class PermisosService {

  constructor(private userService: UserService,
              private appService: AplicationService) { }

  permisos(modulo: number, opcion: number, subMenu: number, permiso: string): boolean {

    var menu = JSON.parse(this.userService.getLocalStorage('menu'));
    
    switch (permiso) {

      case 'ver':

      if(menu[modulo].activo) {

        if (menu[modulo].opciones[opcion].subMenu[subMenu].ver) {
            return true;
        } else {
          this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
          return false;
        }
 
      } else {
        this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
        return false;
      }

      case 'crear' :
  
        if(menu[modulo].activo) {

          if (menu[modulo].opciones[opcion].subMenu[subMenu].ver) {

              return menu[modulo].opciones[opcion].subMenu[subMenu].permisos.crear;
          } else {
        
            this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
            return false;
          }
   
        } else {
          this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
          return false;
        }

      case 'editar' :

        if(menu[modulo].activo) {

          if (menu[modulo].opciones[opcion].subMenu[subMenu].ver) {

              return menu[modulo].opciones[opcion].subMenu[subMenu].permisos.editar;
          } else {
        
            this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
            return false;
          }
   
        } else {
          this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
          return false;
        }

      case 'eliminar' :

        if(menu[modulo].activo) {

          if (menu[modulo].opciones[opcion].subMenu[subMenu].ver) {

              return menu[modulo].opciones[opcion].subMenu[subMenu].permisos.eliminar;
          } else {
        
            this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
            return false;
          }
   
        } else {
          this.appService.swal('Advertencia', 'No tiene permisos para acceder a esta funcionalidad.', 'warning');
          return false;
        }
    }
  }
}
