import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { UserService } from '../user.service';

@Injectable({
  providedIn: 'root'
})
export class RedireccionGuard implements CanActivate {

  constructor(private userService: UserService,
              public router: Router) {

  }

  canActivate() {

    let info = JSON.parse(this.userService.getLocalStorage('user'));

    // console.log(info);
    if (info) {
      switch (info.tipoCuenta) {

        case 'empresa':

        if (info.tipoEmpresa === 'Consultorio') {
          this.router.navigate(['consultorio/home']);
          return true;
        }


        if (info.tipoEmpresa === 'IPS') {
          this.router.navigate(['ips/home']);
          return true;
        }

        if (info.tipoEmpresa === 'Veterinaria') {
          this.router.navigate(['veterinaria/home']);
          return true;
        }

        break;


        case 'empleado' :
          this.router.navigate(['empleado/home']);
          return true;

          case 'medico' :
            this.router.navigate(['medico/home']);
            return true;
      }
    } else {
      this.router.navigate(['login']);
      return false;
    }
  }
}
