import { Component, OnInit } from '@angular/core';
import { SettingsService } from '../../services/settings.service';
import { AplicationService } from '../../services/aplication.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { BodyTipoEmpresa } from '../../interfaces/info';
import Swal from 'sweetalert2';
import { RegistroEmpresa } from '../../interfaces/users';
import { Router } from '@angular/router';
import { UserService } from '../../services/user.service';
import { EmpresaService } from '../../services/empresa.service';
declare var particlesJS: any;

// Funcin para inicializar plugins de la plantilla;
declare function init_plugins();

@Component({
  selector: 'app-registro',
  templateUrl: './registro.component.html',
  styles: [
  ]
})
export class RegistroComponent implements OnInit {

  public buscarDepasMunis;
  public tiposEmpresa: BodyTipoEmpresa[];
  public depasMunis;
  public formRegister: FormGroup;
  public placeholder: string;
  show_button: Boolean = false;
  show_eye: Boolean = false;


  constructor(private settingsService: SettingsService,
    private appService: AplicationService,
    private userService: UserService,
    private empresaService: EmpresaService,
    public router: Router) {
    this.inicializarFormulario();
  }

  ngOnInit(): void {
    particlesJS.load('particles-js', 'assets/particles.json', function () {
      console.log('callback - particles.js config loaded');
    });
    let info = JSON.parse(this.userService.getLocalStorage('user'));

    // console.log(info);

    if (info) {
      this.router.navigate(['']);
      return;
    }


    this.getTipoEMpresa();
    init_plugins();
    this.settingsService.setTema('light');
    this.getDepartamentos();

  }

  // Metodo para obtener los departamentos y municipios
  getDepartamentos() {
    this.appService.getDeparMunis().subscribe(res => this.depasMunis = res);
  }

  // Metodo para obtener los tipos de empresa
  getTipoEMpresa() {
    this.appService.getTiposEmpresa().subscribe((res: BodyTipoEmpresa[]) => this.tiposEmpresa = res);
  }

  selecionarMunicipio(municipio) {

    this.buscarDepasMunis = null;
    (<HTMLInputElement>document.getElementById('idmunicipio')).value = municipio.nombreMunicipio + ' - ' + municipio.nombreDepartamento;
    this.formRegister.get('idmunicipio').setValue(municipio.idmunicipio);
  }

  inicializarFormulario() {

    this.formRegister = new FormGroup({
      idTipoEmpresa: new FormControl(null, Validators.required),
      nombre: new FormControl(null, Validators.required),
      nit: new FormControl(null, Validators.required),
      correo: new FormControl(null, [Validators.required, Validators.email]),
      contrasena: new FormControl(null, [Validators.required]),
      confirmContrasena: new FormControl(null, [Validators.required]),
      aceptoTerminos: new FormControl(null, Validators.requiredTrue)
    });
  }

  deparMuniBuscar(value: string) {
    this.buscarDepasMunis = value;
  }

  registrarEmpresa() {



    if (!this.formRegister.get('aceptoTerminos').value) {
      Swal.fire('Advertencia', 'Por favor acepta terminos y condiciones para continuar con el registro.', 'warning');
      return;
    }
  
    this.appService.swalConfirmInfo('Confimacin de Informacin', 
    `<p>Por favor, antes de continuar con el registro confirma que la informacin sea correcta. Ten en cuenta que para editar datos sensibles es necesario contactar con soporte.</p>
     <br> <p> <strong>Correo: </strong>  ${this.formRegister.get('correo').value}</p> <p><strong>Nit ( sin dgito de verificacin ): </strong> ${this.formRegister.get('nit').value}</p> `)
     .then( () => { 
      let empresa: RegistroEmpresa = {
        contrasena: this.appService.encriptar(this.formRegister.get('contrasena').value),
        correo: this.formRegister.get('correo').value,
        idTipoEmpresa: this.formRegister.get('idTipoEmpresa').value,
        idmunicipio: 1,
        numeroDocumentoUser: this.formRegister.get('nit').value + '-',
        nombre: this.formRegister.get('nombre').value,
        tipoCuenta: 'empresa',
        idtipoDocumentos: 6
    };
  
    // console.log(empresa);
    this.empresaService.postRegistroConsultorio(empresa).subscribe( () => {
        // console.log('aquiii');
        this.router.navigate(['']);
    } );
  
  } );

  }


  advertenciaCorreo(){
    this.appService.swal('Recomendacion', 'Si eres IPS o Consultorio se recomienda tener un correo institucional y no personal', 'warning');
  }
  //Function
  showPassword() {
    this.show_button = !this.show_button;
    this.show_eye = !this.show_eye;
  }

  verificarCorreo() {
    this.appService.verificarCorreoExiste(this.formRegister.get('correo').value).subscribe();
  }

}
