import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { EmpresaService } from '../../services/empresa.service';
import { AplicationService } from '../../services/aplication.service';
import { InfoEmpresa } from '../../interfaces/users';

@Component({
  selector: 'app-modal-cambiar-password',
  templateUrl: './modal-cambiar-password.component.html'
})
export class ModalCambiarPasswordComponent implements OnInit {

  @Input() user;

  contrasena = new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(15)]);
  contrasenaAnterior = new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(15)]);
  confirmarContra = new FormControl('', [Validators.required, Validators.minLength(8), Validators.maxLength(15)]);
  load = false;
  register = false;
  status;
  statusText;
  show_button: Boolean = false;
  show_eye: Boolean = false;
  constructor(private appService: AplicationService) 
    { }

  ngOnInit(): void {
  }


  setTimeLoad() {
    this.load = true;
    setTimeout(() => {
      this.load = false,
        this.register = true;
    }, 500)
  }


  abrirModal() {
    this.load = true;
    document.getElementById('btn-cambiar-pssw2').click();

  }

  changePssw() {


    let info = {
      idMembers: this.user.idMembers,
      contrasena: this.appService.encriptar(this.contrasena.value),
      contrasenaAnterior: this.appService.encriptar(this.contrasenaAnterior.value),
    };

    // console.log(info);
    this.appService.cambioContrasena(info).subscribe();
    // if (this.contrasena.value === this.contrasenaAnterior.value) {
    //   console.log(this.contrasena);
    // } else {
    //   this.appService.swal('', 'Las contraseas no coinciden', 'warning');
    //   this.load = false;
    //   return;

    // }
  }
//Function
showPassword() {
  this.show_button = !this.show_button;
  this.show_eye = !this.show_eye;
}



}
