import { DomSanitizer } from '@angular/platform-browser';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EspecialidadesMedicas } from 'src/app/interfaces/medicos';
import { InfoEmpresa } from 'src/app/interfaces/users';
import { MedicosService } from 'src/app/services/medicos.service';
import { AplicationService } from '../../../services/aplication.service';
import { EmpresaService } from 'src/app/services/empresa.service';
import { InfoServicio } from 'src/app/interfaces/servicios';
import { UserService } from 'src/app/services/user.service';
import { PermisosService } from 'src/app/services/permisos.service';

@Component({
  selector: 'app-servicios',
  templateUrl: './servicios.component.html',
  styles: [
  ]
})

export class ServiciosComponent implements OnInit {
  @Input() user;
  @Input() apiUrl: string;
  @Output() cambioEstado = new EventEmitter<string>();
  @Input() crearEmpresa: boolean;

  public servicios: InfoServicio[];
  public medicos: EspecialidadesMedicas;
  public formServicio: FormGroup;
  public especialidades: EspecialidadesMedicas;
  public especialidadesActivas: any[];
  public fotos: any;
  public palabrasClave = [];
  public palabra;
  public imagenSubir: File;
  public fileName: string;
  public filePreview: string;
  public arrayImagenes: Array<any> = [];
  public especialidadFilter = 'todos';
  public estado = 'crear';
  public servicio: InfoServicio;
  public imagenesEliminar: any = [];
  load = false;
  register = false;

  constructor(
    private medicoService: MedicosService,
    private appService: AplicationService,
    private formBuilder: FormBuilder,
    private sanitizer: DomSanitizer,
    private userService: UserService,
    private empresaService: EmpresaService,
    private permisos: PermisosService) { }

  ngOnInit(): void {
    this.setTimeLoad();

    if (this.crearEmpresa) {
      this.appService.setBreadcrumb('Paso 3 - Crear servicio');
    }

    this.inicializarFormulario();
    this.getInfo();
  }

  setTimeLoad() {
    this.load = true;
    setTimeout(() => {
      this.load = false,
        this.register = true;
    }, 500);
  }
  /**
   * Metodo que permite inicializar otros metodos para que se inicialize en ngOnInit
   * y extraer - departamentos, tipos de documento y personal .
   */
  getInfo() {
    this.getEspecialidadesMedicas();
    this.getEspecialidadesMedicasActivas();
    this.getServicios();
  }

  /**
   * Metodo que permite obtener los servicios a traves del ID.
   */
  getServicios() {
    this.empresaService.getServicios(this.user.idEmpresa).subscribe( res => this.servicios = res );
  }

  /**
   * Metodo para Inicializar el formulario de la empresa, con sus respectivas validaciones.
   */
  // tslint:disable-next-line:typedef
  inicializarFormulario() {

    this.formServicio = this.formBuilder.group({

      idEspecialidad: ['', [Validators.required]],
      nombreServ: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(40)]],
      numeroCitas: ['', [Validators.required]],
      duracionAproximada: [''],
      precioServMosCita: [''],
      precioServCita: ['', [Validators.required, Validators.min(0), Validators.max(99999999)]],
      descuentoCitaServ: ['', [Validators.required]],
      linkYoutServ: [''],
      descServ: ['', [Validators.maxLength(300)]],
      palabrasClave: [''],
      fotos: [''],
    });
  }


  editarServicio(servicio: InfoServicio) {

    if(this.permisos.permisos(1, 1, 0, 'editar')) {

            // console.log(servicio);
            this.servicio = servicio;
            this.estado = 'editar';
            this.imagenesEliminar = [];
      
            this.formServicio.setValue({
              idEspecialidad: servicio.idEspecialidad,
              nombreServ: servicio.nombreServicio,
              numeroCitas: servicio.numeroCitas,
              duracionAproximada : 60 / servicio.numeroCitas,
              precioServMosCita : servicio.precioCita - ((servicio.descuentoCita / 100) * servicio.precioCita),
              precioServCita: servicio.precioCita,
              descuentoCitaServ: servicio.descuentoCita,
              linkYoutServ: servicio.linkVideo,
              descServ: servicio.descripcion,
              palabrasClave : '',
              fotos : ''
            });
      
            servicio.palabrasClave.forEach(palabra => {
              this.palabrasClave.push({palabra});
            });
      
            // console.log(this.formServicio.value);
            document.getElementById('btn-servicio').click();
    }
  }


  putEditarServicio() {

    // (this.servicio.fotos.length + this.arrayImagenes.length) <= 1)
    let infoServ = {
      idServicios : this.servicio.idServicios,
      idEspecialidad: this.formServicio.get('idEspecialidad').value,
      nombreServicio: this.formServicio.get('nombreServ').value,
      numeroCitas: this.formServicio.get('numeroCitas').value,
      duracionAproximada: this.formServicio.get('duracionAproximada').value,
      precioCita: this.formServicio.get('precioServCita').value,
      descuentoCita: this.formServicio.get('descuentoCitaServ').value,
      linkVideo: this.formServicio.get('linkYoutServ').value,
      descripcion: this.formServicio.get('descServ').value,
      activo: this.user.activa,
      idEmpresa: this.user.idEmpresa,
      creadoPor: this.user.idMembers,
      fotos: this.arrayImagenes,
      palabrasClave: this.palabrasClave,
      fotosEliminar : this.imagenesEliminar
    };

    // console.log(infoServ);
    this.empresaService.putEditarServicio(infoServ).subscribe();
  }

  /**
   * Metodo que registra el servicio con el metodo POST 
   * Se usa la interface AgregarMedico como referencia de los parametros
   * activa form medico y si es empleado hace peticion a cargos y activa form empleados
   */
  // tslint:disable-next-line:typedef
  registrarServicio() {

      if (this.arrayImagenes.length <= 0) {
        this.appService.swal('Advertencia', 'El servicio debe contener al menos una imagen', 'warning');
        return;
      }
  
      let infoServ = {
    
        idEspecialidad: this.formServicio.get('idEspecialidad').value,
        nombreServicio: this.formServicio.get('nombreServ').value,
        numeroCitas: this.formServicio.get('numeroCitas').value,
        duracionAproximada: this.formServicio.get('duracionAproximada').value,
        precioCita: this.formServicio.get('precioServCita').value,
        descuentoCita: this.formServicio.get('descuentoCitaServ').value,
        linkVideo: this.formServicio.get('linkYoutServ').value,
        descripcion: this.formServicio.get('descServ').value,
        activo: this.user.activa,
        idEmpresa: this.user.idEmpresa,
        creadoPor: this.user.idMembers,
        fotos: this.arrayImagenes,
        palabrasClave: this.palabrasClave
      };
  
      this.empresaService.postCrearServicio(infoServ).subscribe( () => { 

        setTimeout(() => {
          location.reload();
        }, 900);

      });
    
    
  }

  /**
   * Metodo que permite obtener las especialidades medicas
   * @return res = especialidades en general
  */
  // tslint:disable-next-line:typedef
  getEspecialidadesMedicas() {
    this.medicoService.getEspecialidadesMedicas().subscribe(res => this.especialidades = res);
  }

  /**
   * Metodo que permite obtener las especialidades medicas activas
   * @return res = especialidades activas
  */
  // tslint:disable-next-line:typedef
  getEspecialidadesMedicasActivas() {
    this.empresaService.getEspecialidadesActivas(this.user.idEmpresa).subscribe(res => this.especialidadesActivas = res);
  }

  /**
   * Metodo que cambia la imagen por defecto y muestra la cargada para previsualizacion, 
   * la variable imagenSubir cambia a file para la subida de imagen.
   * @param event
   */
  // tslint:disable-next-line:typedef
  previsualizarImagen(event) {

    if (this.estado === 'editar') {
      if ( (this.servicio.fotos.length + this.arrayImagenes.length) >= 6) {
        this.appService.swal('Advertencia', 'Cada servicio tiene un maximo de 6 imagenes.', 'warning');
        return;
      }
    }

    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0 ) {
      let file = event.target.files[0];
      // console.log(event.target.files);
      this.imagenSubir = file;
      reader.readAsDataURL(file);

      if (file.type === 'image/png' ||
      file.type === 'image/jpg' ||
      file.type === 'image/jpeg' ||
      file.type === 'image/PNG' ||
      file.type === 'image/JPG' ||
      file.type === 'image/JPEG') {
        reader.onload = () => {
          this.fileName = file.name + ' ' + file.type;
          this.filePreview = 'data:image/png' + ';base64,' + ( <string> reader.result ).split(',')[1];
          if (this.arrayImagenes.length <= 5){
            this.arrayImagenes.push(this.filePreview);
          }
          else{
            this.appService.swal('Advertencia', 'Solo se admiten subir 6 imagenes.', 'warning');
          }
        };
    }else{
      this.appService.swal('Advertencia', 'Solo se admiten imagenes tipo png, jpg, jpeg.', 'warning');
    }
  }
}

  /**
   * Metodo para obtener la url de la app para previsualizar imagenes.
   * @return url completa 
   * @param url
   */
  // tslint:disable-next-line:typedef
  sanitize(url: string) {
    return this.sanitizer.bypassSecurityTrustUrl(url);
  }

  /**
   * Metodo que permite eliminar la previsualizacion de imagenes de un servicio segun su indice.
   * @param i
   */
  // tslint:disable-next-line:typedef
  borrarFoto(i) {

    if (this.estado === 'editar' && ((this.servicio.fotos.length + this.arrayImagenes.length) <= 1)) {
      this.appService.swal('Advertencia', 'El servicio debe contener al menos una imagen', 'warning');
      return;
    }

    this.arrayImagenes.splice(i, 1);
   }

   borrarFotoServicio(imagen, posicion: number) {



     if ( (this.servicio.fotos.length + this.arrayImagenes.length) <= 1 ) {
      this.appService.swal('Advertencia', 'El servicio debe contener al menos una imagen', 'warning');
      return;
     }

     this.servicio.fotos.splice(posicion, 1);
     this.imagenesEliminar.push({idFotoServicio: imagen.idfotosServicios, path : imagen.path });

   }

   /**
   * Metodo que permite eliminar la previsualizacion de imagenes de un servicio segun su indice.
   * @param code
   */
  // tslint:disable-next-line:typedef
  keyPressPalabrasClave (code) {
    // console.log(ev);
    if (code === 'Enter' || code === 'Space') {
      // if (this.palabrasClave.length >= 12) {
      //   this.appService.swal('Advertencia', 'Se ha alcanzado el nmero mximo de palabras clave.', 'warning');
      //   return;
      // }
      this.palabrasClave.push({palabra : this.palabra});
      this.palabra = '';
    }
  }

   /**
   * Metodo que permite eliminar la palabra clave con respecto a su indice.
   * @param posicion
   */
  // tslint:disable-next-line:typedef
  borrarPalabra(posicion) {
    this.palabrasClave.splice(posicion, 1);
  }

  /**
   * Metodo que cambiar el estado de pagina
   */
  // tslint:disable-next-line:typedef
  cambiarSwicth() {

    if (this.servicios.length >= 1) {
      this.userService.saveLocalStorage('consultorios', 'estado');
      this.cambioEstado.emit('consultorios');
      return;
    }

    this.appService.swal('Advertencia', 'Por favor agrega al menos un servicio antes de continuar', 'warning');
    }

    eliminarServicioConfirm(servicio: InfoServicio) {

      if( this.permisos.permisos(1, 1, 0, 'eliminar')) {

        this.appService.swalConfirm('Eliminar servicio', ' Estas seguro que deseas eliminar el servicio ?', 'warning')
        .then( () => { this.empresaService.dltServicio(servicio.idServicios).subscribe( () => this.getServicios() );
        });
      }
    }

    crearServicio() {

      if(this.permisos.permisos(1, 1, 0, 'crear')) {
        this.estado = 'crear';
        document.getElementById('btn-servicio').click();
      }
    }

    asociarPublicacion() {

      if(this.user.validada === 0) {
        return this.appService.swal('', 'Por favor contina con el siguiente paso.', 'warning');
      }

      this.appService.swalConfirm('Activar publicacin', 'Para activar una publicacin y sea visible para los usuarios es necesario asociar la publicacin a un consultorio,   Desea hacerlo ahora ?', 'warning').then( () => { 
      // console.log('ir a consultorios');

      let usuario;
      if (this.user.tipoCuenta === 'empresa') {
        usuario = this.user.tipoEmpresa;
      } else {
        usuario = this.user.tipoCuenta;
      }

      this.appService.redireccion(usuario, 'consultorios');
      
      });
    }


    activarInactivarPublicacion() {
      
    }
}