import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'tipoPortada'
})
export class TipoPortadaPipe implements PipeTransform {

  transform(value, type = 'person', primera?) {
    let defaulProfile = '';

    switch (type) {
      case 'medico':
        defaulProfile = 'medico.png'
        break;
      case 'empleado':
        defaulProfile = 'background_default.png'
        break;
      default:
        defaulProfile = 'background_default.png'
        break;
    }

    if (primera) value = value.filter(img => img.tipo == 'portada')[0]

    if (Array.isArray(value) && !primera) {
      // console.log('aaray');
      
      value = value.filter(img => img.tipo == 'portada');
      // console.log(value);
      value.shift()
      // console.log(value);
    }
    //  else if (value instanceof Object) {
    //   value = value.avatar == 'avatarundefined.png' ? null : defaulProfile;
    // }

    // return `${environment.apiUrl}/avatar/${value?.path || value || defaulProfile}`;
    return value;
  }
}