

import { Component, OnInit, ViewChild } from '@angular/core';
import { SettingsService } from '../services/settings.service';
import { UserService } from '../services/user.service';
import { environment } from 'src/environments/environment.prod';
import { ModalCambiarPasswordComponent } from '../components/modal-cambiar-password/modal-cambiar-password.component';
import { AplicationService } from '../services/aplication.service';

declare function init_plugins();
declare function init_appInit();

@Component({ 
  selector: 'app-pages',
  templateUrl: './pages.component.html',
  styles: [
  ]
})
export class PagesComponent implements OnInit {

  @ViewChild(ModalCambiarPasswordComponent) cambiarPssw: ModalCambiarPasswordComponent;

  public user;
  public apiUrl = environment.apiUrl;

  constructor(private settingsService: SettingsService,
              private userService: UserService,
              private appService: AplicationService) { }

  ngOnInit(): void {
    init_appInit();
    init_plugins();
    this.getInfoUser();
    this.settingsService.setTema('light');
    this.user = JSON.parse(this.userService. getLocalStorage('user'));
  }

  abrirModal () {
    // console.log(ev);
    this.cambiarPssw.abrirModal();
  }

  getInfoUser() {
    let infoUser = JSON.parse(localStorage.getItem('user'));
    if (infoUser) {
      this.user = infoUser;

      let menu = localStorage.getItem('menu');
      if (this.user.tipoCuenta === 'empleado' && this.user.sucursales.length > 1  && !menu) {
          document.getElementById('btn-modal-sucuempleado').click();
      }
    } 
  }

  mostrarMenu() {
    this.appService.setBreadcrumb('Home');
    this.settingsService.mostrarMenu.emit( JSON.parse(localStorage.getItem('user')) );
    this.settingsService.actualizarInfo.emit( true );
  }

  validada(bol) {
    // console.log(bol);
    if (bol) {
      this.getInfoUser();
      this.mostrarMenu();
    }
  }

}
